<template>
  <g class="red-select" v-show="isUsingScaleTool">
    <line v-for="n in linesNum"
      :x1="sPoints[n-1].x"
      :y1="sPoints[n-1].y"
      :x2="ePoints[n-1].x"
      :y2="ePoints[n-1].y"
      stroke="red"
      stroke-width="3"
      :key="n">
    </line>
  </g>
</template>

<script>
import { mapGetters } from 'vuex'
import * as Mousetrap from 'mousetrap'

export default {
  name: 'ScaleSelect',

  props: {
    mouseX: Number,
    mouseY: Number,
    mousedownCount: Number
  },

  data () {
    return {
      sPoints: [],
      ePoints: [],
      linesNum: 0,
      mousedownNum: 0
    }
  },

  computed: {
    ...mapGetters([
      'isUsingScaleTool'
    ])
  },

  methods: {
    reset () {
      this.sPoints = []
      this.ePoints = []
      this.linesNum = 0
      this.mousedownNum = 0
    }
  },

  mounted () {
    let mousetrap = new Mousetrap()
    mousetrap.bind('esc', () => {
      if (this.linesNum === 2) {
        if (this.isUsingScaleTool) {
          this.$store.dispatch('scaleSession', {
            sPoints: this.sPoints,
            ePoints: this.ePoints,
            linesNum: this.linesNum
          })
        }
        // if (this.isUsingHorizontalScale) {
        //   this.$store.dispatch('scaleSessionHorizontal', {
        //     sPoints: this.sPoints,
        //     ePoints: this.ePoints,
        //     linesNum: this.linesNum
        //   })
        // }
      }
      this.reset()
    })
  },

  watch: {
    mousedownCount (newVal) {
      if (this.isUsingScaleTool) {
        this.mousedownNum++
        if (this.mousedownNum % 2 === 1) {
          this.sPoints.push({
            x: this.mouseX,
            y: this.mouseY
          })
        }
        if (this.mousedownNum % 2 === 0) {
          this.linesNum ++
          this.ePoints.push({
            x: this.mouseX,
            y: this.mouseY
          })
        }
        // if (this.mousedownNum === 5) {
        //   if (this.isUsingScaleTool) {
        //     this.$store.dispatch('scaleSession', {
        //       sPoints: this.sPoints,
        //       ePoints: this.ePoints,
        //       linesNum: this.linesNum
        //     })
        //   }
        //   if (this.isUsingHorizontalScale) {
        //     this.$store.dispatch('scaleSessionHorizontal', {
        //       sPoints: this.sPoints,
        //       ePoints: this.ePoints,
        //       linesNum: this.linesNum
        //     })
        //   }
        //   this.reset()
        // }
      }
    },

    isUsingScaleTool (newVal) {
      if (newVal) {
        this.reset()
      } else if (this.linesNum === 2) {
        this.$store.dispatch('scaleSession', {
          sPoints: this.sPoints,
          ePoints: this.ePoints,
          linesNum: this.linesNum
        })
      }
    }

    // isUsingHorizontalScale (newVal) {
    //   if (newVal) {
    //     this.reset()
    //   } else if (this.linesNum === 2) {
    //     this.$store.dispatch('scaleSessionHorizontal', {
    //       sPoints: this.sPoints,
    //       ePoints: this.ePoints,
    //       linesNum: this.linesNum
    //     })
    //   }
    // }
  }
}
</script>



