import { getCurvePoints } from 'cardinal-spline-js'
import { StyleNode } from './BezierNode'

class Spline {
  constructor (points) {
    this.points = points
  }
}

// input three points, the middle one is the control point
function getQuadraticBSplinePoints(nodes, sampleNum) {
  let px = nodes.map(node => node.x)
  let py = nodes.map(node => node.y)
  let step = 1 / (sampleNum - 1)
  let outPoints = []
  for (let i = 0; i < sampleNum; i++) {
    let tmpT = i * step
    let p1 = 0.5 * (tmpT - 1) ** 2
    let p2 = 0.5 * (-2 * tmpT * tmpT + 2 * tmpT + 1)
    let p3 = 0.5 * tmpT ** 2
    let tmpX = p1 * px[0] + p2 * px[1] + p3 * px[2]
    let tmpY = p1 * py[0] + p2 * py[1] + p3 * py[2]
    let tmpNode = new StyleNode(tmpX, tmpY)
    outPoints.push(tmpNode)
  }
  return outPoints
}

// sample number between two control points
function cubicFit (nodes, sampleNum) {
  let path = []
  let orgPoints = []
  nodes.forEach(node => {
    orgPoints.push(node.x)
    orgPoints.push(node.y)
  })
  let outPoints = getCurvePoints(orgPoints, 0.5, sampleNum)
  let len = outPoints.length / 2
  for (let i = 0; i < len - 1; i++) {
    let tmpX = outPoints[2 * i]
    let tmpY = outPoints[2 * i + 1]
    let tmpStyleNode = new StyleNode(tmpX, tmpY)
    path.push(tmpStyleNode)
  }
  return path
}

export class CubicSplineSimple extends Spline {
  constructor (points) {
    super(points)
    this.flatSampleNum = 4
    this.stepSampleNum = 8
    this.tension = 0.15
  }

  fit () {
    // console.log('fit')
    let points = this.points
    let path = this.points.slice()
    for (let i = 0; i < points.length - 1; ) {
      if (Math.abs(points[i].y - points[i + 1].y) < 0.001) {
        // first seg found
        for (let j = i + 2; j < points.length - 1; j++) {
          if (Math.abs(points[j].y - points[j + 1].y) < 0.001) {
            if (Math.abs(points[i].y - points[j].y) > 0.001) {
              // segs with diffrent y
              let distance = j - i - 1
              let middleNodesCount = distance - 1
              if (middleNodesCount < 0) {
                // what?
                console.error('middleNode less than 0')
              } else if (middleNodesCount < 3) {
                let middle
                if (middleNodesCount === 0) {
                  middle = new StyleNode((points[i + 1].x + points[j].x) / 2, (points[i + 1].y + points[j].y) / 2)
                } else {
                  let middleIndex = middleNodesCount === 1 ? i + 2 : i + 3 
                  middle = new StyleNode(points[middleIndex].x, points[middleIndex].y)
                }
                let result = getQuadraticBSplinePoints([points[i], points[i + 1], middle], this.stepSampleNum)
                result = result.concat(getQuadraticBSplinePoints([middle, points[j], points[j + 1]], this.stepSampleNum))
                path[i + 1] = result
                for (let k = i + 2; k <= i + 2 + middleNodesCount; k++) {
                  path[k] = undefined
                }
              } else {
                let nodes = points.slice(i + 1, j + 1)
                // console.log(nodes.length)
                let result = nodes.length > 3 ? cubicFit(nodes, 2) : getQuadraticBSplinePoints(nodes, (nodes.length + 1) * this.stepSampleNum)
                // let result = getQuadraticBSplinePoints(nodes, (nodes.length + 1) * this.stepSampleNum)
                path[i + 1] = result
                for (let k = i + 2; k <= j + 1; k++) {
                  path[k] = undefined
                }
              }
            } else {
              // first seg match second
            }
            // let second seg as first
            i = j - 1
            break
          }
        }
        i++
      } else {
        i++
      }
    }
    // flatmap here
    let result = []
    path.forEach(v => {
      if (v) {
        if (Array.isArray(v)) {
          v.forEach(n => {
            result.push(n)
          })
        } else {
          result.push(v)
        }
      }
    })
    return result
  }
}


