export class BezierNode {
  constructor(x, y, x2, y2) {
    this._activate = false;
    this._last = null;
    // point
    this._x = x;
    this._y = y;
    this._xBak = x;
    this._yBak = y;
    // control point
    this._x2 = x2;
    this._y2 = y2;
    this._xBak2 = x2;
    this._yBak2 = y2;
    // reversed control point
    this._x3 = 2 * x2 - x;
    this._y3 = 2 * y2 - y;
    this._xBak3 = this._x3;
    this._yBak3 = this._y3;
  }

  get isActiveBezierTool() {
    return this._activate;
  }

  set lastNode(node) {
    this._last = node;
  }

  get lastNode() {
    return this._last;
  }

  get x() {
    return this._x;
  }

  set x(value) {
    this._x = value
  }

  get y() {
    return this._y;
  }

  set y(value) {
    this._y = value;
  }

  get x2() {
    return this._x2;
  }

  set x2(value) {
    this._x2 = value;
  }

  get y2() {
    return this._y2;
  }

  set y2(value) {
    this._y2 = value;
  }

  set x3(value) {
    this._x3 = value;
  }

  get x3 () {
    return this._x3;
  }

  get y3 () {
    return this._y3;
  }

  set y3(value) {
    this._y3 = value;
  }
  // backup (x, y, x2, y2) before updating and rotating
  backup() {
    this._xBak = this._x;
    this._yBak = this._y;
    this._xBak2 = this._x2;
    this._yBak2 = this._y2;
    this._xBak3 = this._x3;
    this._yBak3 = this._y3;
  }

  activateBezierTool() {
    this._activate = true;
  }

  closeBezierTool() {
    this._activate = false;
  }

  move(moveX, moveY) {
    this._x += moveX;
    this._y += moveY;
    this._x2 += moveX;
    this._y2 += moveY;
    this._x3 += moveX;
    this._y3 += moveY;
  }

  update(distX, distY) {
    this._x = this._xBak + distX;
    this._y = this._yBak + distY;
    this._x2 = this._xBak2 + distX;
    this._y2 = this._yBak2 + distY;
    this._x3 = this._xBak3 + distX;
    this._y3 = this._yBak3 + distY;
  }

  updateX (distX) {
    this._x = this._xBak + distX;
    this._x2 = this._xBak2 + distX;
    this._x3 = this._xBak3 + distX;
  }

  updateY (distY) {
    this._y = this._yBak + distY;
    this._y2 = this._yBak2 + distY;
    this._y3 = this._yBak3 + distY;
  }

  rotate(sin, cos, refX, refY) {
    this._x = (this._xBak - refX) * cos - (this._yBak - refY) * sin + refX
    this._y = (this._xBak - refX) * sin + (this._yBak - refY) * cos + refY
    this._x2 = (this._xBak2 - refX) * cos - (this._yBak2 - refY) * sin + refX
    this._y2 = (this._xBak2 - refX) * sin + (this._yBak2 - refY) * cos + refY
    this._x3 = (this._xBak3 - refX) * cos - (this._yBak3 - refY) * sin + refX
    this._y3 = (this._xBak3 - refX) * sin + (this._yBak3 - refY) * cos + refY
  }
}

export class Node {
  constructor (x, y) {
    this.x = x
    this._xBak = x
    this.y = y
    this._yBak = y
    this.isOptimized = false
    this.isOptimizedTmp = false
  }

  backup () {
    this._xBak = this.x
    this._yBak = this.y
  }

  move (moveX, moveY) {
    this.x += moveX
    this.y += moveY
  }

  updateX (distX) {
    this.x = this._xBak + distX
  }

  updateY (distY) {
    this.y = this.y > -10000 ? this._yBak + distY : -10000
  }

  rotate(sin, cos, refX, refY) {
    this.x = (this._xBak - refX) * cos - (this._yBak - refY) * sin + refX
    this.y = (this._xBak - refX) * sin + (this._yBak - refY) * cos + refY
  }
}

export class StyleNode {
  constructor (x, y) {
    this.x = x
    this.y = y
  }
}


