<template>
  <div  v-tooltip="'Curving'">
    <font-awesome-icon :icon="icon" />
  </div>
</template>

<script>
import Icon from '@fortawesome/fontawesome-free-solid/faPaintBrush'

export default {
  name: 'CurveTool',

  computed: {
    icon () {
      return Icon
    }
  }
}
</script>




