<template>
  <div class="tree-panel">
    <ul v-for="(item, index) in objectsTree" :key="index">
      <tree-node v-if="item.children.length == 1" :model="item.children[0]"></tree-node>
      <tree-node v-else :model="item"></tree-node>
    </ul>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'TreePanel',

  computed: {
    ...mapGetters([
      'objectsTree'
    ])
  }
}
</script>

<style lang="scss">
.tree-panel {
  padding: 5px 12px;
}

.bold {
  font-weight: bold;
}

ul {
  padding-left: 1em;
  line-height: 1.5em;
  margin: 0px;
}
</style>




