export class CardObject {
  constructor (id) {
    this.id = id
    this.zIndex = 0
    this.geometry = {
      positionX: 0,
      positionY: 0,
      width: 100,
      height: 100
    }
  }

  get ID () {
    return this.id
  }

  get width () {
    return this.geometry.width
  }

  get height () {
    return this.geometry.height
  }

  get positionX () {
    return this.geometry.positionX
  }

  get positionY () {
    return this.geometry.positionY
  }

  get depth () {
    return this.zIndex
  }

  updateID (id) {
    this.id = id
  }

  updateGeometry (x, y, w, h) {
    this.geometry.positionX = x
    this.geometry.positionY = y
    this.geometry.width = w
    this.geometry.height = h
  }

  updateZIndex (zIndex) {
    this.zIndex = zIndex
  }
}


