import { EquivalentIndexFinder } from './EquivalentIndexFinder'

export class StorySelector {
  constructor(frameMap, sessionMap, characterMap) {
    this.frameMap = frameMap
    this.sessionMap = sessionMap
    this.characterMap = characterMap
  }

  matchFrame (x, y) {
    for (let [frame, range] of this.frameMap) {
      if ((x - range[0]) * (x - range[1]) <= 0) {
        return frame
      }
    }
    return -1
  }

  matchFrameUnBound (x, y) {
    let minBound = 1e4
    let maxFrame = 0
    for (let [frame, range] of this.frameMap) {
      minBound = range[0] < minBound ? range[0] : minBound
      maxFrame = frame
      if ((x - range[0]) * (x - range[1]) <= 0) {
        return frame
      }
    }
    return x < minBound ? 0 : maxFrame
  }

  matchCharacter (x, y) {
    let frame = this.matchFrame(x, y)
    if (frame > -1) {
      let characterYMap = this.characterMap.get(frame)
      for (let [characterId, characterY] of characterYMap) {
        if (Math.abs(y - characterY) < 2) {
          return characterId
        }
      }
    }
    return -1
  }
  
  matchCharacterR (frame, y) {
    if (frame > -1) {
      let characterYMap = this.characterMap.get(frame)
      for (let [characterId, characterY] of characterYMap) {
        if (Math.abs(y - characterY) < 10) {
          return characterId
        }
      }
    }
    return -1
  }

  matchSession (x, y) {
    let frame = this.matchFrame(x, y)
    if (frame > -1) {
      let characterYMap = this.characterMap.get(frame)
      for (let [characterId, characterY] of characterYMap) {
        if (Math.abs(y - characterY) < 2) {
          return this.sessionMap[characterId][frame]
        }
      }
    } else {
      return -1
    }
  }
}

export class EmphasizeSelector extends StorySelector {
  constructor (rawStorylines, frameTable, sessionTable, characterTable, request, storyConfig) {
    super(frameTable, sessionTable, characterTable)
    this.rawStorylines = rawStorylines
    this.defaultInnerGap = request.sessionInnerGap
    this.defaultOuterGap = request.sessionOuterGap
    this.yScaleFactor = storyConfig.yScaleFactor
    this.yBaseline = storyConfig.paddingTop
  }

  getSelectSessionList (sArr, eArr) {
    let startX = sArr[0].x < eArr[0].x ? sArr[0].x : eArr[0].x
    let endX = sArr[0].x < eArr[0].x ? sArr[0].x : eArr[0].x
    let sFrame = this.matchFrameUnBound(startX, 0)
    let eFrame = this.matchFrameUnBound(endX, 0)
    let topY = sArr[0].y < eArr[0].y ? sArr[0].y : eArr[0].y
    let bottomY = sArr[0].y > eArr[0].y ? sArr[0].y : eArr[0].y
    let sessionList = []
    for (let frame = sFrame; frame < eFrame + 1; frame++) {
      for (let id = 0, len = this.rawStorylines.length; id < len; id++) {
        let characterId = this.rawStorylines[id].character_id
        let positionY = this.rawStorylines[id].points[frame].item3 * this.yScaleFactor + this.yBaseline
        if (positionY >= topY && positionY <= bottomY) {
          let sessionId = this.sessionMap[characterId][frame]
          if (sessionId > -1 && sessionList.indexOf(sessionId) === -1) {
            sessionList.push(sessionId)
          }
        }
      }
    }
    return sessionList
  }

  getScaleFactor (sArr, eArr) {
    return Math.abs((eArr[1].y - sArr[1].y) / (eArr[0].y - sArr[0].y))
  }

  buildSessionConstraints (sArr, eArr) {
    let sessionList = this.getSelectSessionList(sArr, eArr)
    let scaleFactor = this.getScaleFactor(sArr, eArr)
    let scaleInnerGap = this.defaultInnerGap * scaleFactor
    // let scaleOuterGap = this.defaultOuterGap * scaleFactor
    let scaleOuterGap = Math.abs(eArr[1].y - sArr[1].y)
    // debugger
    let sessionInnerGaps = []
    sessionList.forEach(sessionId => {
      sessionInnerGaps.push({
        'item1': sessionId,
        'item2': scaleInnerGap
      })
    })
    let sessionOuterGaps = []
    // TODO: change current outergap
    for (let i = 0, len = sessionList.length; i < len; i++) {
      for (let j = i + 1; j < len; j++) {
        sessionOuterGaps.push({
          'item1': {
            'item1': sessionList[i],
            'item2': sessionList[j]
          },
          'item2': {
            'item1': scaleOuterGap,
            // 'item2': scaleOuterGap + 30
            'item2': -1
          }
        })
      }
    }
    return {
      'sessionInnerGaps': sessionInnerGaps,
      'sessionOuterGaps': sessionOuterGaps
    }
  }
}

export class StoryReorder extends StorySelector {
  constructor(storylines, storyConfig, frameMap, sessionMap, characterMap) {
    super(frameMap, sessionMap, characterMap)
    this.storylines = storylines
    this.storyConfig = storyConfig
    this.frame = -1
    this.character = -1
    this.session = -1
    // record initial y position of storyline
    this.yBak1 = 0
    this.yBak2 = 0
    this.newY = 0
  }

  locate (x, y) {
    this.frame = this.matchFrame(x, y)
    if (this.frame > -1) {
      this.character = this.matchCharacterR(this.frame, y)
      if (this.character > -1) {
        // let characterYMap = this.characterMap.get(this.frame)
        let storyline = this.storylines[this.character].points
        this.yBak1 = this.getPositionY(storyline, 0)
        this.yBak2 = this.getPositionY(storyline, 1)
      }
    }
  }

  getPositionY (storyline, pos) {
    let frame = this.frame
    if (frame > -1) {
      let lIndex = 0
      let rIndex = 1
      if (frame > 0) {
        // lIndex = 3 + 4 * (frame - 1) + 1
        // rIndex = 3 + 4 * (frame - 1) + 2
        lIndex = 2 * frame
        rIndex = lIndex + 1
      }
      switch (pos) {
        case 0:
        return storyline[lIndex].y > -10000 ? storyline[lIndex].y : storyline[rIndex].y
        case 1:
        return storyline[rIndex].y
      }
    }
    return -10000
  }

  move (moveY) {
    let yScaleFactor = this.storyConfig.yScaleFactor
    let yBaseline = this.storyConfig.paddingTop
    let scaleMoveY = moveY / yScaleFactor
    if (this.character > -1 && this.frame > -1) {
      let storyline = this.storylines[this.character].points
      this.moveStoryline(storyline, scaleMoveY)
      this.newY = ((this.yBak1 + this.yBak2) / 2 + scaleMoveY) * yScaleFactor + yBaseline
    }
  }

  moveStoryline (storyline, moveY) {
    let frame = this.frame
    if (frame > -1) {
      let lIndex = 0
      let rIndex = 1
      if (frame > 0) {
        // lIndex = 3 + 4 * (frame - 1) + 1
        // rIndex = 3 + 4 * (frame - 1) + 2
        lIndex = 2 * frame
        rIndex = lIndex + 1
      }
      storyline[lIndex].y = storyline[lIndex].y > -10000 ? this.yBak1 + moveY : storyline[lIndex].y
      storyline[rIndex].y = this.yBak2 + moveY
    }
  }

  reorder () {
    if (this.frame > -1) {
      let cOrder = this.reorderCharacter()
      let cSession = this.generateSessionOrder(cOrder.order)
      let sOrder = this.reorderSession()
      let sSession = this.generateSessionOrder(sOrder.order)
      return this.isArrayEqual(cSession, sSession) ? cOrder : sOrder
    }
  }

  isArrayEqual (arr1, arr2) {
    if (arr1.length === arr2.length) {
      for (let i = 0, len = arr1.length; i < len; i++) {
        if (arr1[i] !== arr2[i]) {
          return false
        }
      }
      return true
    }
    return false
  }

  generateSessionOrder (arr) {
    let cMap = new Map()
    arr.forEach((pos, c) => {
      if (pos > -1) {
        let sessionId = this.sessionMap[c][this.frame]
        if (sessionId > -1) {
          if (cMap.has(sessionId)) {
            if (cMap.get(sessionId) > pos) {
              cMap.set(sessionId, pos)
            }
          } else {
            cMap.set(sessionId, pos)
          }
        }
      }
    })
    let cArr = []
    for (let [session, pos] of cMap) {
      cArr.push({
        session: session,
        pos: pos
      })
    }
    cArr.sort((a, b) => a.pos - b.pos)
    return cArr.map(v => v.session)
  }

  reorderCharacter () {
    let finalList = []    
    if (this.character > -1) {
      // init session nodes
      let len = this.sessionMap.length
      let characterInSessionMap = new Map()
      for (let character = 0; character < len; character++) {
        let tmpSession = this.sessionMap[character][this.frame]
        if (this.character === character) {
          this.session = tmpSession
        }
        if (tmpSession > -1) {
          if (characterInSessionMap.has(tmpSession)) {
            let tmpList = characterInSessionMap.get(tmpSession)
            tmpList.push(character)
          } else {
            characterInSessionMap.set(tmpSession, [character])
          }
        }
      }
      // sort characters of session node
      let characterYMap = this.characterMap.get(this.frame)
      let sessionYMap = new Map()
      for (let [session, characters] of characterInSessionMap) {
        let sortedCharacters = []
        let sessionY = 0
        characters.forEach(character => {
          let yPos = this.character === character ? this.newY : characterYMap.get(character)
          let flag = false
          for (let i = 0; i < sortedCharacters.length; i++) {
            let tmpCharacter = sortedCharacters[i]
            let tmpYPos = this.character === tmpCharacter ? this.newY : characterYMap.get(tmpCharacter)
            if (yPos < tmpYPos) {
              sortedCharacters.splice(i, 0, character)
              flag = true
              break
            }
          }
          if (!flag) {
            sortedCharacters.push(character)
          }
          sessionY += characterYMap.get(character)
        })
        sessionY = sessionY / characters.length
        sessionYMap.set(session, sessionY)
        characterInSessionMap.set(session, sortedCharacters)
      }
      // sort session nodes
      let sortedSessions = []
      let sortedList = []
      for (let [session, characters] of characterInSessionMap) {
        let flag = false
        for (let i = 0; i < sortedSessions.length; i++) {
          let tmpSession = sortedSessions[i]
          if (sessionYMap.get(session) < sessionYMap.get(tmpSession)) {
            sortedSessions.splice(i, 0, session)
            sortedList.splice(i, 0, characters)
            flag = true
            break
          }
        }
        if (!flag) {
          sortedSessions.push(session)
          sortedList.push(characters)
        }
      }
      // sort characters
      let unionList = []
      sortedList.forEach(tmpList => {
        unionList = unionList.concat(tmpList)
      })
      // except rabbit case
      let rabbitId = this.sessionMap.length - 1
      let _unionList = []
      unionList.forEach(id => {
        if (id !== rabbitId) {
          _unionList.push(id)
        }
      })
      _unionList.splice(0, 0, rabbitId)
      unionList = _unionList
      // console.log(unionList)
      // format finalList
      let finalMap = new Map()
      unionList.forEach((character, pos) => {
        finalMap.set(character, pos)
      })
      for (let k = 0; k < len; k++) {
        if (finalMap.has(k)) {
          finalList.push(finalMap.get(k))
        } else {
          finalList.push(-1)
        }
      }
    }
    return {
      frame: this.frame,
      order: finalList
    }
  }

  reorderSession () {
    let finalList = []    
    if (this.character > -1) {
      // init session nodes
      let len = this.sessionMap.length
      let characterInSessionMap = new Map()
      for (let character = 0; character < len; character++) {
        let tmpSession = this.sessionMap[character][this.frame]
        if (this.character === character) {
          this.session = tmpSession
        }
        if (tmpSession > -1) {
          if (characterInSessionMap.has(tmpSession)) {
            let tmpList = characterInSessionMap.get(tmpSession)
            tmpList.push(character)
          } else {
            characterInSessionMap.set(tmpSession, [character])
          }
        }
      }
      // sort characters of session node
      let characterYMap = this.characterMap.get(this.frame)
      let sessionYMap = new Map()
      for (let [session, characters] of characterInSessionMap) {
        let sortedCharacters = []
        let sessionY = 0
        characters.forEach(character => {
          let yPos = characterYMap.get(character)
          let flag = false
          for (let i = 0; i < sortedCharacters.length; i++) {
            let tmpCharacter = sortedCharacters[i]
            let tmpYPos = characterYMap.get(tmpCharacter)
            if (yPos < tmpYPos) {
              sortedCharacters.splice(i, 0, character)
              flag = true
              break
            }
          }
          if (!flag) {
            sortedCharacters.push(character)
          }
          sessionY += yPos
        })
        sessionY = this.session === session ? this.newY : sessionY / characters.length
        sessionYMap.set(session, sessionY)
        characterInSessionMap.set(session, sortedCharacters)
      }
      // sort session nodes
      let sortedSessions = []
      let sortedList = []
      for (let [session, characters] of characterInSessionMap) {
        let flag = false
        for (let i = 0; i < sortedSessions.length; i++) {
          let tmpSession = sortedSessions[i]
          if (sessionYMap.get(session) < sessionYMap.get(tmpSession)) {
            sortedSessions.splice(i, 0, session)
            sortedList.splice(i, 0, characters)
            flag = true
            break
          }
        }
        if (!flag) {
          sortedSessions.push(session)
          sortedList.push(characters)
        }
      }
      // sort characters
      let unionList = []
      sortedList.forEach(tmpList => {
        unionList = unionList.concat(tmpList)
      })
      // except rabbit case
      let rabbitId = this.sessionMap.length - 1
      let _unionList = []
      unionList.forEach(id => {
        if (id !== rabbitId) {
          _unionList.push(id)
        }
      })
      _unionList.splice(0, 0, rabbitId)
      unionList = _unionList
      // console.log(unionList)
      // format finalList
      let finalMap = new Map()
      unionList.forEach((character, pos) => {
        finalMap.set(character, pos)
      })
      for (let k = 0; k < len; k++) {
        if (finalMap.has(k)) {
          finalList.push(finalMap.get(k))
        } else {
          finalList.push(-1)
        }
      }
    }
    return {
      frame: this.frame,
      order: finalList
    }
  }
}

export class Merger extends StorySelector {
  constructor(frameMap, sessionMap, characterMap) {
    super(frameMap, sessionMap, characterMap)
  }

  merge(x, y) {
    let session = matchSession (x, y)
    let result
    if (session !== -1) {
      result = {
        "item1": session,
        "item2": 0
      }
    }

    return result
  }
}

export class Spliter extends StorySelector {
  constructor(frameMap, sessionMap, characterMap, defaultInnerGap) {
    super(frameMap, sessionMap, characterMap)
    this.defaultInnerGap = defaultInnerGap
  }

  split(x, y) {
    let session = matchSession (x, y)
    let result
    if (session !== -1) {
      result = {
        "item1": session,
        "item2": this.defaultInnerGap
      }
    }

    return result
  }
}

export class FrameScaler extends StorySelector {
  constructor(frameMap, sessionMap, characterMap, frameSteps, curveSteps) {
    super(frameMap, sessionMap, characterMap)
    this.frameSteps = frameSteps
    this.curveSteps = curveSteps
  }

  scale(sPoints, ePoints) {
    let x1 = sPoints[0].x
    let x2 = ePoints[0].x
    let x3 = sPoints[1].x
    let x4 = ePoints[1].x
    let startFrame = this.matchFrameUnBound(x1, 0)
    let endFrame = this.matchFrameUnBound(x2, 0)
    let scaleFactor = Math.abs(x4 - x3) / Math.abs(x2 - x1)
    if (x2 > x1) {
      console.log('horizontal scale curve step')
      this.scaleCurveStep(startFrame, endFrame, scaleFactor)
    } else if (x2 < x1) {
      console.log('horizontal scale frame step')      
      this.scaleFrameStep(startFrame, endFrame, scaleFactor)
    }
  }
  
  scaleCurveStep (startFrame, endFrame, scaleFactor) {
    if (endFrame - 1 >= startFrame + 1) {
      for (let frame = 0; frame < this.frameMap.size; frame++) {
        // reduce selection sensitivity
        if (startFrame + 1 <= frame && frame <= endFrame - 1) {
          this.curveSteps[frame] *= scaleFactor
        }
      }
    }
  }
  
  scaleFrameStep (startFrame, endFrame, scaleFactor) {
    if (startFrame - 1 >= endFrame + 1) {
      for (let frame = 0; frame < this.frameMap.size; frame++) {
        if (endFrame + 1 <= frame && frame <= startFrame - 1) {
          this.frameSteps[frame] *= scaleFactor
        }
      }
    }
  }
}

export class SessionMover extends StorySelector {
  constructor (storylines, frameTable, sessionTable, characterTable) {
    super(frameTable, sessionTable, characterTable)
    this.storylines = storylines
    this.moveTable = new Map()
    this.yBak1 = new Map()
    this.yBak2 = new Map()
  }

  locate (x, y) {
    this.frame = this.matchFrame(x, y)
    if (this.frame > -1) {
      this.character = this.matchCharacterR(this.frame, y)
      if (this.character > -1) {
        let equivalentIndexFinder = new EquivalentIndexFinder(this.sessionMap)
        this.moveTable = equivalentIndexFinder.find(this.character, this.frame)
        for (let [character, frameRange] of this.moveTable) {
          let storyline = this.storylines[character].points
          let tmpFrame = frameRange[0]
          let tmpLeftY = this.getPositionY(storyline, tmpFrame, 0)
          let tmpRightY = this.getPositionY(storyline, tmpFrame, 1)
          this.yBak1.set(character, tmpLeftY)
          this.yBak2.set(character, tmpRightY)
        }
      }
    }
  }

  getPositionY (storyline, frame, pos) {
    if (frame > -1) {
      let lIndex = 0
      let rIndex = 1
      if (frame > 0) {
        lIndex = 2 * frame
        rIndex = lIndex + 1
      }
      switch (pos) {
        case 0:
        return storyline[lIndex].y > -10000 ? storyline[lIndex].y : storyline[rIndex].y
        case 1:
        return storyline[rIndex].y
      }
    }
    return -10000
  }

  move (moveY) {
    let frame = this.frame
    let character = this.character
    let moveTable = this.moveTable
    if (frame > -1 && character > -1) {
      for (let [character, frameRange] of moveTable) {
        this.moveStoryline(moveY, character, frameRange)
      }
    }
  }

  moveStoryline (moveY, character, frameRange) {
    let storyline = this.storylines[character].points
    frameRange.forEach(frame => {
      let lIndex = 2 * frame
      let rIndex = lIndex + 1
      let tmpYBak1 = this.yBak1.get(character)
      let tmpYBak2 = this.yBak2.get(character)
      storyline[lIndex].y = storyline[lIndex].y > -10000 ? tmpYBak1 + moveY : storyline[lIndex].y
      storyline[rIndex].y = tmpYBak2 + moveY
      // update character map
      let tmpY = (storyline[lIndex].y + storyline[rIndex].y) / 2
      let tmpMap = this.characterMap.get(frame)
      tmpMap.set(character, tmpY)
    })
  }
}


