import Vue from 'vue'
import Vuex from 'vuex'

import StyleStore from './modules/StyleStore'
import ObjectStore from './modules/ObjectStore'
import ToolStore from './modules/ToolStore'
import TextStore from './modules/TextStore'
import CardStore from './modules/CardStore'
import PanelStore from './modules/PanelStore'
import StoryStore from './modules/StoryStore'

import { RESTORE_DEFAULT_OBJECTS, CHANGE_TOOL } from './MutationTypes'

Vue.use(Vuex)

const state = {
  isObjectsActiveChange: false,
  usingToolName: ''
}

const getters = {
  isObjectsActiveChange: state => state.isObjectsActiveChange,
  usingToolName: state => state.usingToolName
}

const mutations = {
  [RESTORE_DEFAULT_OBJECTS] (state) {
    state.isObjectsActiveChange = !state.isObjectsActiveChange
  },

  [CHANGE_TOOL] (state, tool) {
    state.usingToolName = tool
  }
}

export default new Vuex.Store({
  modules: {
    StyleStore,
    ObjectStore,
    ToolStore,
    TextStore,
    CardStore,
    PanelStore,
    StoryStore
  },
  state,
  getters,
  mutations
})



