import { Node, StyleNode } from './BezierNode'
import { getCurvePoints } from 'cardinal-spline-js'

const ALPHA = 0
const ALIGN = 4.0

export class PathReshaperFrame {
  constructor (storylines, minFrame, maxFrame, config, targetPath, sessionTable) {
    this.storylines = storylines
    this.minFrame = minFrame
    this.maxFrame = maxFrame
    this.config = config
    this.sessionTable = sessionTable
    this.targetPath = targetPath
  }

  moveFocusCharacter (focusCharacter) {
    this.resetOptimization()
    let storyline = this.storylines[focusCharacter].points
    // let fitter = new LinearFitCurve(this.targetPath)
    let fitter = new CubicFitCurve(this.targetPath)
    let [start, end] = fitter.match(storyline, this.minFrame, this.maxFrame, this.config)
    let contextMap = this.setOptimizingCharacters(focusCharacter, start, end)
    for (let i = start, len = end + 1; i < len; i++) {
      let contextCharacters = contextMap.get(i)
      let newY = fitter.fitCurve(storyline[i])
      if (newY > -10000) {
        // alignment case
        newY = this.alignContextPoint(i, newY, storyline)
        let moveY = newY - storyline[i].y
        this.moveContextCharacters(i, contextCharacters, focusCharacter, moveY)
        storyline[i].y = newY
      }
    }
  }

  resetOptimization () {
    this.storylines.forEach(storyline => {
      storyline.points.forEach(point => {
        point.isOptimizedTmp = false
      })
    })
  }

  setOptimizingCharacters (focusCharacter, start, end) {
    let contextCharactersMap = new Map()
    for (let i = start, len = end + 1; i < len; i++) {
      let tmpContextCharacters = this.scanInSessionCharacters(i, focusCharacter)
      contextCharactersMap.set(i, tmpContextCharacters)
    }
    return contextCharactersMap
  }

  scanInSessionCharacters (i, character) {
    let sortingCharacters = []
    let frame = this.generateFrame(i)
    let session = this.sessionTable[character][frame]
    if (session > -1) {
      for (let id = 0, len = this.sessionTable.length; id < len; id++) {
        if (this.sessionTable[id][frame] === session) {
          sortingCharacters.push({
            id: id,
            y: this.storylines[id].points[i].y
          })
          this.storylines[id].points[i].isOptimized = true
          this.storylines[id].points[i].isOptimizedTmp = true
        }
      }
      sortingCharacters.sort((a, b) => {
        return a.y - b.y
      })
    }    
    return sortingCharacters.map(d => d.id)
  }

  generateFrame (i) {
    return parseInt(i / 2)
  }

  moveContextCharacters (i, characters, focusCharacter, focusMoveY) {
    let upper = []
    let lower = []
    let isUpper = true
    // divide into upper and lower case
    characters.forEach(character => {
      if (character === focusCharacter) {
        isUpper = false
      }
      if (isUpper) {
        upper.push(character)
      } else {
        lower.push(character)
      }
    })
    upper.push(focusCharacter)
    upper = upper.reverse()
    // upper case
    let lastCharacterMoveY = focusMoveY
    upper.forEach(character => {
      if (character !== focusCharacter) {
        let storyline = this.storylines[character].points
        let oldY = storyline[i].y
        if (oldY > -10000) {
          let newY = oldY + lastCharacterMoveY
          storyline[i].y = this.alignContextPoint(i, newY, storyline)
          storyline[i].isOptimized = true
          lastCharacterMoveY = storyline[i].y - oldY
        }
      }
    })
    // lower case
    lastCharacterMoveY = focusMoveY
    lower.forEach(character => {
      if (character !== focusCharacter) {
        let storyline = this.storylines[character].points
        let oldY = storyline[i].y
        if (oldY > -10000) {
          let newY = oldY + lastCharacterMoveY
          storyline[i].y = this.alignContextPoint(i, newY, storyline)
          storyline[i].isOptimized = true
          lastCharacterMoveY = storyline[i].y - oldY
        }
      }
    })
  }

  alignContextPoint (i, newY, storyline) {
    let len = storyline.length
    let isLeftAlign = false
    let isRightAlign = false
    let leftAnchorY = 0
    let rightAnchorY = 0
    let baseY = storyline[i].y
    let thres = 5
    if (i > 1) {
      let leftPoint = storyline[i - 1]
      if (!leftPoint.isOptimizedTmp) {
        leftAnchorY = leftPoint.y
        if (leftAnchorY > -10000 && Math.abs(baseY - leftAnchorY) < thres) {
          // return (newY + ALIGN * leftAnchorY) / (1 + ALIGN)
          isLeftAlign = true
        }
      }
    }
    if (i < len - 1) {
      let rightPoint = storyline[i + 1]
      if (!rightPoint.isOptimizedTmp) {
        rightAnchorY = rightPoint.y
        if (rightAnchorY > -10000 && Math.abs(baseY - rightAnchorY) < thres) {
          isRightAlign = true
        }
      }
    }
    let upper = newY
    let lower = 1
    if (isLeftAlign) {
      upper += ALIGN * leftAnchorY
      lower += ALIGN
    }
    if (isRightAlign) {
      upper += ALIGN * rightAnchorY
      lower += ALIGN
    }
    return upper / lower
  }
}

export class PathReshaper {
  constructor (storylines, sessionTable, frameTable, storyConfig, targetPath) {
    this.storylines = storylines
    this.sessionTable = sessionTable
    this.frameTable = frameTable
    this.config = storyConfig
    this.targetPath = targetPath
  }

  moveFocusCharacter (focusCharacter) {
    let storyline = this.storylines[focusCharacter].points
    let fitter = new CubicFitCurve(this.targetPath)
    let [start, end] = fitter.matchStoryline(storyline, this.config)
    // TODO: Consider context characters
    // console.log(fitter.fittedCurve)
    if (start > -1 && end > -1) {
      for (let i = start, len = end + 1; i < len; i++) {
        let newY = fitter.fitCurve(storyline[i])
        if (newY > -10000) {
          // console.log(focusCharacter, i, newY, storyline[i].y)
          storyline[i].y = newY
        }
      }
    }
  }

  reshapeCharacters (characters) {
    characters.forEach(character => {
      this.moveFocusCharacter(character)
    })
  }
}

export class PathReshaperSingle {
  constructor (targetPath) {
    this.targetPaths = [targetPath]
  }

  moveFocusCharacter (storyline) {
    this.targetPaths.forEach(targetPath => {
      if (targetPath.length > 0) {
        let fitter = new CubicFitCurve(targetPath)
        let [start, end] = fitter.matchStoryline(storyline, this.config)
        if (start > -1 && end > -1) {
          for (let i = start, len = end + 1; i < len; i++) {
            let newY = fitter.fitCurve(storyline[i])
            if (newY > -10000) {
              let oldY = storyline[i].y
              storyline[i].y = newY
            }
          }
        }
      }
    })
  }
}

class FitCurve {
  constructor (targetPath) {
    // original path
    this.targetPath = targetPath
    // fitted path
    this.fittedCurve = []
  }

  // relocate at the storyline coordinates
  syncrize (config) {
    this.fittedCurve = []
    // let xScale = config.xScaleFactor
    // let yScale = config.yScaleFactor
    let xScale = 1
    let yScale = 1
    let xBase = 0
    let yBase = 0
    this.targetPath.forEach(point => {
      let x = (point.x - xBase) / xScale
      let y = (point.y - yBase) / yScale
      let fittedNode = new StyleNode(x, y)
      this.fittedCurve.push(fittedNode)
    })
    this.fittedCurve.sort((a, b) => {
      return a.x - b.x
    })
  }

  matchStoryline (storyline, config) {
    this.syncrize(config)
    let [leftIndex, rightIndex] = this.locate(storyline)
    if (leftIndex > -1 && rightIndex > -1) {
      let leftPoint = storyline[leftIndex]
      let rightPoint = storyline[rightIndex]
      let n = this.targetPath.length - 1
      let firstPoint = new StyleNode(this.fittedCurve[0].x, this.fittedCurve[0].y)
      let lastPoint = new StyleNode(this.fittedCurve[n].x, this.fittedCurve[n].y)
      // special case: ending point can be flexible adjusted
      // TODO: consider point start not from the beginning
      if (rightIndex === storyline.length - 1) {
        this.fittedCurve.forEach(point => {
          point.x = leftPoint.x + (point.x - firstPoint.x)
          point.y = leftPoint.y + (point.y - firstPoint.y)
        })
      } else {
        let xScale = Math.abs((rightPoint.x - leftPoint.x) / (lastPoint.x - firstPoint.x))
        let yScale = Math.abs((rightPoint.y - leftPoint.y) / (lastPoint.y - firstPoint.y))
        this.fittedCurve.forEach(point => {
          point.x = leftPoint.x + xScale * (point.x - firstPoint.x)
          point.y = leftPoint.y + yScale * (point.y - firstPoint.y)
        })
      }
    }
    return [leftIndex, rightIndex]
  }

  locate (storyline) {
    let n = this.targetPath.length - 1
    let firstTargetPoint = this.fittedCurve[0]
    let lastTargetPoint = this.fittedCurve[n]
    let isFirstPointAnchored = false
    let isLastPointAnchored = false
    let leftBoundIndex = -1
    let rightBoundIndex = storyline.length - 1
    for (let i = 1, len = storyline.length - 1; i < len; i++) {
      // TODO: select the nearest point
      if (storyline[i].y > -10000) {
        if (!isFirstPointAnchored && storyline[i].x > firstTargetPoint.x) {
          leftBoundIndex =  storyline[i - 1].y > -10000 ? i - 1 : i
          // while (!this.isFlatPoint(storyline, leftBoundIndex++)) {}
          isFirstPointAnchored = true
        }
        if (!isLastPointAnchored && storyline[i].x > lastTargetPoint.x) {
          rightBoundIndex = i
          isLastPointAnchored = true
        }
      }
    }
    return [leftBoundIndex, rightBoundIndex]
  }

  isFlatPoint (storyline, i) {
    if (storyline[i].y === -10000) {
      return false
    }
    if (i === 0) {
      return true
    }
    let leftI = i - 1
    while (leftI > 0 && storyline[leftI--].y === -10000) {}
    let rightI = i + 1
    while (rightI > 0 && storyline[rightI++].y === -10000) {}
    if (storyline[leftI + 1].y === storyline[i].y || storyline[rightI - 1].y === storyline[i].y) {
      return true
    }
    return false
  }

  match (storyline, minFrame, maxFrame, config) {
    this.syncrize(config)
    let leftIndex = this.getLeftPointIndex(storyline, minFrame)
    let leftPoint = storyline[leftIndex]
    let rightIndex = this.getRightPointIndex(storyline, maxFrame)
    let rightPoint = storyline[rightIndex]
    let firstPoint = new Node(this.fittedCurve[0].x, this.fittedCurve[0].y)
    let n = this.fittedCurve.length - 1
    let lastPoint = new Node(this.fittedCurve[n].x, this.fittedCurve[n].y)
    // special case: ending point can be flexible adjusted
    // if (rightIndex === storyline.length - 1) {
    //   this.fittedCurve.forEach(point => {
    //     point.x = leftPoint.x + (point.x - firstPoint.x)
    //     point.y = leftPoint.y + (point.y - firstPoint.y)
    //   })
    // } else {
    //   let xScale = Math.abs((rightPoint.x - leftPoint.x) / (lastPoint.x - firstPoint.x))
    //   let yScale = Math.abs((rightPoint.y - leftPoint.y) / (lastPoint.y - firstPoint.y))
    //   this.fittedCurve.forEach(point => {
    //     point.x = leftPoint.x + xScale * (point.x - firstPoint.x)
    //     point.y = leftPoint.y + yScale * (point.y - firstPoint.y)
    //   })
    // }
    this.fittedCurve.forEach(point => {
      point.x = leftPoint.x + (point.x - firstPoint.x)
      point.y = leftPoint.y + (point.y - firstPoint.y)
    })
    return [leftIndex, rightIndex]
  }

  getLeftPointIndex (storyline, minFrame) {
    let index = 2 * minFrame
    // filter un-qualified nodes
    while (storyline[index++].y === -10000) {}
    return index - 1
  }

  getRightPointIndex (storyline, maxFrame) {
    let index = 2 * maxFrame + 1
    // filter un-qualified nodes
    while (storyline[index--].y === -10000) {}
    return index + 1
  }
}

// For Testing
class LinearFitCurve extends FitCurve {
  constructor (targetPath) {
    super(targetPath)
  }

  fitCurve (point) {
    for (let i = 0, len = this.fittedCurve.length - 1; i < len; i++) {
      // locate at a range
      if ((point.x - this.fittedCurve[i].x) * (point.x - this.fittedCurve[i + 1].x) <= 0) {
        let tempK = (this.fittedCurve[i + 1].y - this.fittedCurve[i].y) / (this.fittedCurve[i + 1].x - this.fittedCurve[i].x)
        let targetY = this.fittedCurve[i].y + tempK * (point.x - this.fittedCurve[i].x)
        return point.y === -10000 ? -10000 : (targetY + ALPHA * point.y) / (1 + ALPHA)
      }
    }
    return point.y
  }
}

class CubicFitCurve extends FitCurve {
  constructor (targetPath) {
    super(targetPath)
  }

  match (storyline, minFrame, maxFrame, config) {
    // scale target path
    let range = super.match(storyline, minFrame, maxFrame, config)
    // cubic fit target path
    let tmpArr = this.fittedCurve
    let inPoints = []
    this.fittedCurve.forEach(point => {
      inPoints.push(point.x)
      inPoints.push(point.y)
    })
    let samplesNum = 2
    let outPoints = getCurvePoints(inPoints, 0.5, samplesNum)
    this.fittedCurve = []
    for (let i = 0, len = outPoints.length / 2; i < len; i++) {
      let tmpX = outPoints[2 * i]
      let tmpY = outPoints[2 * i + 1]
      let tmpNode = new Node(tmpX, tmpY)
      this.fittedCurve.push(tmpNode)
    }
    return range
  }

  matchStoryline(storyline, config) {
    let range = super.matchStoryline(storyline, config)
    // cubic fit target path
    let tmpArr = this.fittedCurve
    let inPoints = []
    this.fittedCurve.forEach(point => {
      inPoints.push(point.x)
      inPoints.push(point.y)
    })
    let samplesNum = 2
    let outPoints = getCurvePoints(inPoints, 0.5, samplesNum)
    this.fittedCurve = []
    for (let i = 0, len = outPoints.length / 2; i < len; i++) {
      let tmpX = outPoints[2 * i]
      let tmpY = outPoints[2 * i + 1]
      let tmpNode = new StyleNode(tmpX, tmpY)
      this.fittedCurve.push(tmpNode)
    }
    return range
  }

  fitCurve (point) {
    for (let i = 0, len = this.fittedCurve.length - 1; i < len; i++) {
      // locate at a range
      if ((point.x - this.fittedCurve[i].x) * (point.x - this.fittedCurve[i + 1].x) <= 0) {
        let tempK = (this.fittedCurve[i + 1].y - this.fittedCurve[i].y) / (this.fittedCurve[i + 1].x - this.fittedCurve[i].x)
        let targetY = this.fittedCurve[i].y + tempK * (point.x - this.fittedCurve[i].x)
        if (point.y === -10000) {
          return -10000
        } else if (point.y === -5000) {
          // console.log(targetY)
          return targetY
        } else if (point.y > 0) {
          return (targetY + ALPHA * point.y) / (1 + ALPHA)
        }
      }
    }
    return point.y
  }
}


