<template>
  <div class="svg-card"
    @mousedown="handleMousedown"
    :style="styleObject">
    <svg class="svg-content">
      <visual-object v-for="(object, index) in objects"
        v-if="object.type !== 'text'"
        :object="object"
        :mouseX="mouseX"
        :mouseY="mouseY"
        :mouseupCount="mouseupCount"
        :mousedownCount="mousedownCount"
        :key="index">
      </visual-object>
      <frame-select
        :mouseX="mouseX"
        :mouseY="mouseY"
        :mousedownCount="mousedownCount">
      </frame-select>
      <scale-select
        :mouseX="mouseX"
        :mouseY="mouseY"
        :mousedownCount="mousedownCount">
      </scale-select>
      <path-select
        :mouseX="mouseX"
        :mouseY="mouseY"
        :mousedownCount="mousedownCount">
      </path-select>
    </svg>
    <text-object v-for="(object, index) in textObjects"
      :mouse="mouse"
      :object="object"
      :scrollX="scrollX"
      :scrollY="scrollY"
      :key="index">
    </text-object>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { UPDATE_SVG_CARD, LOCATE_TEXT_OBJECT } from '../../../store/MutationTypes'
import { Text } from '../../../types/VisualObject'
import VisualObject from '../objects/VisualObject'
import GroupSelect from '../utils/GroupSelect'
import ScaleSelect from '../utils/ScaleSelect'
import PathSelect from '../utils/PathSelect'
import TextObject from '../objects/TextObject'

export default {
  name: 'SVGCard',

  props: {
    mouse: Object,
    cardObject: Object,
    scrollX: Number,
    scrollY: Number
  },

  data () {
    return {
      cardEl: {},
      storyReorder: null,
      sessionMover: null,
      reorderRecordY: 0
    }
  },

  computed: {
    cardID () {
      return this.cardObject.ID
    },

    cardWidth () {
      return this.cardObject.width
    },

    cardHeight () {
      return this.cardObject.height
    },

    cardDepth () {
      return this.cardObject.depth
    },

    cardPositionX () {
      return this.cardObject.positionX
    },

    cardPositionY () {
      return this.cardObject.positionY
    },

    styleObject () {
      let cursorType = ''
      if (this.isUsingCurveTool) {
        cursorType = 'crosshair'
      }
      if (this.isUsingBendTool) {
        cursorType = 'crosshair'
      }
      if (this.isUsingScaleTool) {
        cursorType = 'crosshair'
      }
      if (this.isUsingTextTool) {
        cursorType = 'text'
      }
      return {
        width: `${this.cardWidth}px`,
        height: `${this.cardHeight}px`,
        padding: `${this.cardPositionY}px ${this.cardPositionX}px`,
        cursor: cursorType
      }
    },

    mouseX () {
      return this.mouse.positionX - this.cardPositionX + this.scrollX
    },

    mouseY () {
      return this.mouse.positionY - this.cardPositionY + this.scrollY
    },

    mousedownCount () {
      return this.mouse.downCount
    },

    mouseupCount () {
      return this.mouse.upCount
    },

    ...mapGetters([
      'paintingCardList',
      'isUsingCurveTool',
      'isUsingBendTool',
      'isUsingScaleTool',
      'isUsingTextTool',
      'objects',
      'textObjects'
    ])
  },

  methods: {
    handleMousedown () {
      if (this.isUsingTextTool) {
        let x = this.mouse.positionX + this.scrollX
        let y = this.mouse.positionY + this.scrollY
        let textObj = new Text('text', x, y)
        this.$store.dispatch('insertTextObject', textObj)
        this.$store.commit(LOCATE_TEXT_OBJECT, textObj)
      }
    }
  },

  mounted () {
    // update svg size
    let paintingEl = document.getElementsByClassName('painting')[0]
    let paintingW = paintingEl.clientWidth
    let paintingH = paintingEl.clientHeight
    let positionX = 0.05 * paintingW
    let positionY = 0.02 * paintingH
    let svgCardW = paintingW - 2 * positionX
    let svgCardH = paintingH - 2 * positionY
    this.paintingCardList[0].updateGeometry(positionX, positionY, svgCardW, svgCardH)
    this.$store.commit(UPDATE_SVG_CARD, {
      canvasW: svgCardW,
      canvasH: svgCardH
    })
  },

  components: {
    'visual-object': VisualObject,
    'frame-select': GroupSelect,
    'scale-select': ScaleSelect,
    'path-select': PathSelect,
    'text-object': TextObject
  }
}
</script>

<style lang="scss">
.svg-card {
  .svg-content {
    width: 100%;
    height: 100%;
    background: white;
    border-radius: 2px;
    box-shadow: 3px 3px 2px grey;
  }
}
</style>



