<template>
  <g class="rectangle" v-show="visible">
    <path :d="pathD"
      :fill="fillColor"
      :stroke="strokeColor"
      :stroke-width="strokeWidth"
      :opacity="opacity">
    </path>
    <controlnode v-for="(node, index) in nodes"
      v-show="reshape"
      :mouseX="mouseX"
      :mouseY="mouseY"
      :mouseupCount="mouseupCount"
      :mousedownCount="mousedownCount"
      :node="node"
      :key="index">
    </controlnode>
  </g>
</template>

<script>
import ControlNode from '../utils/ControlNode'

export default {
  name: 'Rectangle',

  props: ['mouseX', 'mouseY', 'mousedownCount', 'mouseupCount', 'fillColor', 'strokeColor', 'strokeWidth', 'opacity', 'object'],

  // init rectangle
  mounted () {
    if (!this.object.isCreate) {
      this.object.addBezierNode(10, 10, 10, 10)
      this.object.addBezierNode(100, 10, 100, 10)
      this.object.addBezierNode(100, 100, 100, 100)
      this.object.addBezierNode(10, 100, 10, 100)
      this.object.init()
    }
  },

  computed: {
    nodes () {
      return this.object.nodes
    },

    visible () {
      return this.object.isShow
    },

    reshape () {
      return this.object.isReshape
    },

    pathD () {
      let pathD = ''
      this.nodes.forEach((node, index) => {
        if (index === 0) {
          pathD += `M ${node.x} ${node.y}`
        } else {
          pathD += ` L ${node.x} ${node.y}`
        }
      })
      if (this.nodes.length > 2) {
        pathD += ` L ${this.nodes[0].x} ${this.nodes[0].y}`
      }
      return pathD
    }
  },

  components: {
    controlnode: ControlNode
  }
}
</script>



