<template>
  <div class="box-card"
    :style="position">
    <div class="state"
      v-for="(state, index) in states"
      @click="handleClick(state)"
      :key="index">
      {{state}}
    </div>
  </div>
</template>

<script>
export default {
  name: 'ContextMenu',

  props: ['recordX', 'recordY', 'scrollX', 'scrollY'],

  data () {
    return {
      states: [
        'Hide',
        'Scale',
        'Reshape',
        'Copy',
        // 'Cut',
        'Paste',
        'Group',
        'Ungroup',
        'Cancel'
      ]
    }
  },

  computed: {
    position () {
      return {
        top: `${this.recordY}px`,
        left: `${this.recordX}px`
      }
    }
  },

  methods: {
    handleClick (state) {
      switch (state) {
        case 'Reshape':
          this.$store.dispatch('reshapeFocusObject')
          break
        case 'Scale':
          this.$store.dispatch('scaleFocusObject')
          break
        case 'Hide':
          this.$store.dispatch('hideFocusObject')
          break
        case 'Cancel':
          this.$store.dispatch('cancelFocusObject')
          break
        case 'Copy':
          this.$store.dispatch('copyFocusObject')
          break
        case 'Paste':
          this.$store.dispatch('pasteFocusObject', {
            x: this.recordX + this.scrollX,
            y: this.recordY + this.scrollY
          })
          break
        case 'Cut':
          this.$store.dispatch('cutFocusObject')
          break
        case 'Group':
          this.$store.dispatch('groupObjects')
          break
        default:
          break
      }
    }
  }
}
</script>

<style lang="scss">
  .box-card {
    position: absolute;
    padding: 0px;
    border: 1px solid #ddd;

    .state {
      width: 160px;
      padding: 8px 10px;
      font-size: 14px;
      text-align: center;
      border-bottom: 1px solid #ddd;
    }

    .state:hover {
      background-color: #EEE;
    }
  }
</style>



