<template>
  <div  v-tooltip="'Bending'">
    <font-awesome-icon :icon="icon" />
  </div>
</template>

<script>
import Icon from '@fortawesome/fontawesome-free-solid/faHandPaper'

export default {
  name: 'BendTool',

  computed: {
    icon () {
      return Icon
    }
  }
}
</script>


