<template>
  <g class="BezierTool">
    <line stroke="black"
      :x1="node.x3"
      :y1="node.y3"
      :x2="node.x2"
      :y2="node.y2"
      stroke-width="1">
    </line>
    <circle r=3
      :cx="node.x3"
      :cy="node.y3"
      @mousedown="handleMousedownForSymmetryBezierNode"
      fill="black">
    </circle>
    <circle r=3
      :cx="node.x2"
      :cy="node.y2"
      @mousedown="handleMousedownForBezierNode"
      fill="black">
    </circle>
  </g>
</template>

<script>
export default {
  name: 'BezierTool',

  props: ['node', 'mouseX', 'mouseY', 'mousedownCount', 'mouseupCount'],

  data () {
    return {
      isBezierNodeDragged: false,
      isSymmetryBezierNodeDragged: false,
      xInNodeCoordinate: 0,
      yInNodeCoordinate: 0,
      fixedLength: 0,
      fixedSymmetryLength: 0,
      deltaX: 0,
      deltaY: 0
    }
  },

  methods: {
    handleMousedownForBezierNode () {
      this.isBezierNodeDragged = true
      this.xInNodeCoordinate = this.mouseX - this.node.x2
      this.yInNodeCoordinate = this.mouseY - this.node.y2
      this.deltaX = this.node.x2 - this.node.x
      this.deltaY = this.node.y2 - this.node.y
      this.fixedLength = Math.hypot(this.deltaX, this.deltaY)
      let _deltaX = this.node.x3 - this.node.x
      let _deltaY = this.node.y3 - this.node.y
      this.fixedSymmetryLength = Math.hypot(_deltaX, _deltaY)
    },

    handleMousedownForSymmetryBezierNode () {
      this.isSymmetryBezierNodeDragged = true
      this.xInNodeCoordinate = this.mouseX - this.node.x3
      this.yInNodeCoordinate = this.mouseY - this.node.y3
      this.deltaX = this.node.x3 - this.node.x
      this.deltaY = this.node.y3 - this.node.y
      this.fixedSymmetryLength = Math.hypot(this.deltaX, this.deltaY)
      let _deltaX = this.node.x2 - this.node.x
      let _deltaY = this.node.y2 - this.node.y
      this.fixedLength = Math.hypot(_deltaX, _deltaY)
    },

    moveBezierNodeX (mouseX) {
      this.node.x2 = mouseX
      this.deltaX = this.node.x2 - this.node.x
      let newLength = Math.hypot(this.deltaX, this.deltaY)
      let k = this.fixedSymmetryLength / newLength
      this.node.x3 = k * (this.node.x - mouseX) + this.node.x
    },

    moveBezierNodeY (mouseY) {
      this.node.y2 = mouseY
      this.deltaY = this.node.y2 - this.node.y
      let newLength = Math.hypot(this.deltaX, this.deltaY)
      let k = this.fixedSymmetryLength / newLength
      this.node.y3 = k * (this.node.y - mouseY) + this.node.y
    },

    moveSymmetryBezierNodeX (mouseX) {
      this.node.x3 = mouseX
      this.deltaX = this.node.x3 - this.node.x
      let newLength = Math.hypot(this.deltaX, this.deltaY)
      let k = this.fixedLength / newLength
      this.node.x2 = k * (this.node.x - mouseX) + this.node.x
    },

    moveSymmetryBezierNodeY (mouseY) {
      this.node.y3 = mouseY
      this.deltaY = this.node.y3 - this.node.y
      let newLength = Math.hypot(this.deltaX, this.deltaY)
      let k = this.fixedLength / newLength
      this.node.y2 = k * (this.node.y - mouseY) + this.node.y
    }
  },

  watch: {
    mouseupCount () {
      this.isBezierNodeDragged = false
      this.isSymmetryBezierNodeDragged = false
    },

    mouseX (newVal) {
      if (this.isBezierNodeDragged) {
        this.moveBezierNodeX(newVal)
      }
      if (this.isSymmetryBezierNodeDragged) {
        this.moveSymmetryBezierNodeX(newVal)
      }
    },

    mouseY (newVal) {
      if (this.isBezierNodeDragged) {
        this.moveBezierNodeY(newVal)
      }
      if (this.isSymmetryBezierNodeDragged) {
        this.moveSymmetryBezierNodeY(newVal)
      }
    }
  }
}
</script>



