<template>
  <g class="rect-frame">
    <rect
      fill="none"
      :x="updateX"
      :y="updateY"
      :width="updateW"
      :height="updateH"
      stroke="blue">
    </rect>
    <rect v-for="(node, key) in nodes"
      :style="node.cursor"
      :x="node.x - 3"
      :y="node.y - 3"
      :width="6"
      :height="6"
      fill="white"
      stroke="blue"
      stroke-width="1"
      @mousedown="handleMousedown(key)"
      @mouseup="handleMouseup"
      :key="key">
    </rect>
    <line
      :x1="positionX"
      :y1="positionY"
      :x2="rotateNode.x"
      :y2="rotateNode.y"
      stroke="blue"
      stroke-width="1">
    </line> 
    <circle 
      fill = "blue"
      :cx="rotateNode.x"
      :cy="rotateNode.y"
      :r=3
      stroke="black"
      stroke-width="1"
      @mousedown="handleRotateMousedown"
      @mouseup="handleRotateMouseup">
    </circle>
    <circle 
      fill = "blue"
      :cx="positionX"
      :cy="positionY"
      :r=3
      stroke="black"
      stroke-width="1"
      @mousedown="handleRotateMousedown"
      @mouseup="handleRotateMouseup">
    </circle>
  </g>
</template>

<script>
export default {
  name: 'RectFrame',

  props: {
    mouseX: Number,
    mouseY: Number,
    frameX: Number,
    frameY: Number,
    frameW: Number,
    frameH: Number
  },

  data () {
    return {
      a: 0,
      b: 0,
      c: 0,
      draggedNode: '',
      frameUpdateCount: 0,
      xMouseInFrame: 0,
      yMouseInFrame: 0,
      rx: 0,
      ry: -30,
      sin: 0,
      cos: 0,
      updateX: this.frameX || 0,
      updateY: this.frameY || 0,
      updateW: this.frameW || 0,
      updateH: this.frameH || 0,
      oldW: this.frameW || 0,
      oldH: this.frameH || 0
    }
  },

  computed: {
    nodes () {
      return {
        tPoint: { x: this.updateX + this.updateW / 2, y: this.updateY, cursor: { 'cursor': 'n-resize' } },
        rPoint: { x: this.updateX + this.updateW, y: this.updateY + this.updateH / 2, cursor: { 'cursor': 'e-resize' } },
        bPoint: { x: this.updateX + this.updateW / 2, y: this.updateY + this.updateH, cursor: { 'cursor': 's-resize' } },
        lPoint: { x: this.updateX, y: this.updateY + this.updateH / 2, cursor: { 'cursor': 'w-resize' } },
        tlPoint: { x: this.updateX, y: this.updateY, cursor: { 'cursor': 'nw-resize' } },
        trPoint: { x: this.updateX + this.updateW, y: this.updateY, cursor: { 'cursor': 'ne-resize' } },
        brPoint: { x: this.updateX + this.updateW, y: this.updateY + this.updateH, cursor: { 'cursor': 'se-resize' } },
        blPoint: { x: this.updateX, y: this.updateY + this.updateH, cursor: { 'cursor': 'sw-resize' } }
      }
    },

    positionX () {
      return this.updateX + this.updateW / 2
    },

    positionY () {
      return this.updateY + this.updateH / 2
    },

    rotateNode () {
      return {
        x: this.positionX + this.rx,
        y: this.positionY + this.ry
      }
    }
  },

  methods: {
    handleMousedown (key) {
      this.draggedNode = key
      this.xMouseInFrame = this.mouseX - this.updateX
      this.yMouseInFrame = this.mouseY - this.updateY
      this.oldW = this.updateW
      this.oldH = this.updateH
    },

    handleMouseup () {
      this.draggedNode = ''
      this.$emit('updateFrame', {
        x: this.updateX,
        y: this.updateY,
        w: this.updateW,
        h: this.updateH
      })
    },

    handleRotateMousedown () {
      this.draggedNode = 'circle'
      let a = this.rotateNode.x - this.positionX
      let b = this.positionY - this.rotateNode.y
      let c = Math.hypot(a, b)
      this.sin = a / c
      this.cos = b / c
    },

    handleRotateMouseup () {
      this.draggedNode = ''
      let a = this.rotateNode.x - this.positionX
      let b = this.positionY - this.rotateNode.y
      let c = Math.hypot(a, b)
      this.$emit('rotate', {
        sin: (a / c) * this.cos - (b / c) * this.sin,
        cos: (b / c) * this.cos + (a / c) * this.sin
      })
    }

  },

  watch: {
    mouseX (newVal) {
      let newX = newVal - this.xMouseInFrame
      let newW = this.updateW
      switch (this.draggedNode) {
        case 'rPoint':
        case 'trPoint':
        case 'brPoint':
          newW = this.oldW + newX - this.updateX
          if (newW > 0) {
            this.$emit('updateW', newW)
            this.updateW = newW
          }
          break
        case 'lPoint':
        case 'tlPoint':
        case 'blPoint':
          newW = this.updateW - (newX - this.updateX)
          if (newW > 0) {
            this.$emit('updateX', newW)
            this.updateX = newX
            this.updateW = newW
          }
          break
        case 'circle':
          this.rx = newVal - this.positionX
          let a = this.rotateNode.x - this.positionX
          let b = this.positionY - this.rotateNode.y
          let c = Math.hypot(a, b)
          this.$emit('rotate', { sin: (a / c) * this.cos - (b / c) * this.sin, cos: (b / c) * this.cos + (a / c) * this.sin })
          break
        default:
          break
      }
    },

    mouseY (newVal) {
      let newY = newVal - this.yMouseInFrame
      let newH = this.frameH
      switch (this.draggedNode) {
        case 'tPoint':
        case 'tlPoint':
        case 'trPoint':
          newH = this.updateH - (newY - this.updateY)
          if (newH > 0) {
            this.$emit('updateY', newH)
            this.updateY = newY
            this.updateH = newH
          }
          break
        case 'bPoint':
        case 'brPoint':
        case 'blPoint':
          newH = this.oldH + (newY - this.updateY)
          if (newH > 0) {
            this.$emit('updateH', newH)
            this.updateH = newH
          }
          break
        case 'circle':
          this.ry = newVal - this.positionY
          let a = this.rotateNode.x - this.positionX
          let b = this.positionY - this.rotateNode.y
          let c = Math.hypot(a, b)
          console.log(a, b, c)
          this.$emit('rotate', { sin: (a / c) * this.cos - (b / c) * this.sin, cos: (b / c) * this.cos + (a / c) * this.sin })
          break
        default:
          break
      }
    },

    frameX (newVal) {
      this.updateX = newVal
    },

    frameY (newVal) {
      this.updateY = newVal
    },

    frameW (newVal) {
      this.updateW = newVal
    },

    frameH (newVal) {
      this.updateH = newVal
    }
  }
}
</script>



