<template>
  <li>
    <div class="tree-node-content"
      :class="{bold: isFolder}"
      @dblclick="changeVisible">
      <p v-if="visible">{{ model.name }}
        <span v-if="isFolder" @click="toggle">[{{ open ? '-' : '+' }}]</span>
      </p>
      <s v-else>{{ model.name }}
        <span v-if="isFolder" @click="toggle">[{{ open ? '-' : '+' }}]</span>
      </s>
    </div>
    <ul v-show="open" v-if="isFolder">
      <tree-node
        class="tree-node"
        v-for="(model, index) in model.children"
        :key="index"
        :model="model">
      </tree-node>
      <!-- <li class="add" @click="addChild">+</li> -->
    </ul>
  </li>
</template>

<script>
export default {
  name: 'TreeNode',

  props: {
    model: Object
  },

  data () {
    return {
      open: false
    }
  },

  computed: {
    isFolder () {
      return this.model.children && this.model.children.length
    },

    visible () {
      return this.model.isShow
    }
  },

  methods: {
    toggle () {
      if (this.isFolder) {
        this.open = !this.open
      }
    },

    changeVisible () {
      if (this.model.isShow) {
        this.model.closeShow()
      } else {
        this.model.activateShow()
      }
    },

    changeType () {
      if (!this.isFolder) {
        this.$set(this.model, 'children', [])
        this.addChild()
        this.open = true
      }
    },

    addChild () {
      this.model.children.push({
        name: 'new element'
      })
    }
  }
}
</script>

<style lang="scss">
.tree-node-content {
  font-size: 24px;
  p {
    margin: 0px;
  }
}
</style>



