<template>
  <g class="custom-shape" v-show="visible">
    <path
      :d="pathD"
      :fill="fillColor"
      :stroke="strokeColor"
      :stroke-width="strokeWidth"
      :opacity="opacity">
    </path>
    <g v-for="(node, index) in nodes" :key="index">
      <beziertool v-show="node.isActiveBezierTool"
        :mouseX="mouseX"
        :mouseY="mouseY"
        :mouseupCount="mouseupCount"
        :mousedownCount="mousedownCount"
        :node="node">
      </beziertool>
      <controlnode v-show="!created || reshape"
        @deleteNode="deleteNode(index)"
        :mouseX="mouseX"
        :mouseY="mouseY"
        :mouseupCount="mouseupCount"
        :mousedownCount="mousedownCount"
        :index="index"
        :node="node">
      </controlnode>
    </g>
  </g>
</template>

<script>
import { mapGetters } from 'vuex'
import { INSERT_VISUAL_OBJECT } from '../../../store/MutationTypes'
import BezierTool from '../utils/BezierTool'
import ControlNode from '../utils/ControlNode'
import { CustomShape } from '../../../types/VisualObject'

import * as Mousetrap from 'mousetrap'

export default {
  name: 'CustomShape',

  props: ['mouseX', 'mouseY', 'mousedownCount', 'mouseupCount', 'fillColor', 'strokeColor', 'strokeWidth', 'opacity', 'object'],

  data () {
    return {
      draggedNodeId: -1,
      xReferedToNode: 0,
      yReferedToNode: 0
    }
  },

  computed: {
    nodes () {
      return this.object.nodes
    },

    visible () {
      return this.object.isShow
    },

    reshape () {
      return this.object.isReshape
    },

    created () {
      return this.object.isCreate
    },

    pathD () {
      let pathD = ''
      this.nodes.forEach((node, index) => {
        if (index === 0) {
          pathD += `M ${node.x} ${node.y}`
        } else {
          let lastNode = this.nodes[index - 1]
          pathD += ` C ${lastNode.x3} ${lastNode.y3} ${node.x2} ${node.y2} ${node.x} ${node.y}`
        }
      })
      pathD = this.nodes.length > 2 ? pathD += 'Z' : pathD
      return pathD
    },

    ...mapGetters([
      'isUsingPenTool',
      'objects'
    ])
  },

  methods: {
    deleteNode (index) {
      this.object.deleteBezierNode(index)
    }
  },

  watch: {
    mousedownCount (newVal) {
      if (!this.created) {
        this.draggedNodeId = this.object.addBezierNode(this.mouseX, this.mouseY, this.mouseX, this.mouseY)
        // debugger
        this.nodes[this.draggedNodeId].activateBezierTool()
      }
    },

    mouseupCount (newVal) {
      if (!this.created && this.draggedNodeId > -1) {
        this.nodes[this.draggedNodeId].closeBezierTool()
      }
    },

    mouseX (newVal) {
      if (!this.created) {
        let ID = this.draggedNodeId
        if (ID > -1) {
          let node = this.nodes[ID]
          if (node.isActiveBezierTool) {
            node.x2 = newVal
            node.x3 = 2 * node.x - newVal
          }
        }
      }
    },

    mouseY (newVal) {
      if (!this.created) {
        let ID = this.draggedNodeId
        if (ID > -1) {
          let node = this.nodes[ID]
          if (node.isActiveBezierTool) {
            node.y2 = newVal
            node.y3 = 2 * node.y - newVal
          }
        }
      }
    },

    isUsingPenTool (newVal) {
      if (!this.object.isCreate) {
        this.object.init()
      }
    }
  },

  // IMPORTANT
  mounted () {
    let mousetrap = new Mousetrap()
    mousetrap.bind('esc', () => {
      this.object.init()
      // begin next path
      let customShape = new CustomShape('customshape')
      this.$store.commit(INSERT_VISUAL_OBJECT, customShape)
    })
  },

  components: {
    beziertool: BezierTool,
    controlnode: ControlNode
  }
}
</script>



