import { CardObject } from '../../types/CardObject'

const state = {
  paintingCardList: [],
  paintingCardCount: 0
}

const getters = {
  paintingCardCount: state => state.paintingCardCount,
  paintingCardList: state => state.paintingCardList
}

const mutations = {}

const actions = {
  addPaintingCard ({ state }) {
    let id = state.paintingCardCount++
    let card = new CardObject(id)
    state.paintingCardList.push(card)
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}



