<template>
  <g class="group-select" v-show="isUsingFrameSelect">
    <line v-for="n in linesNum"
      :x1="sPoints[n-1].x"
      :y1="sPoints[n-1].y"
      :x2="ePoints[n-1].x"
      :y2="ePoints[n-1].y"
      stroke="red"
      stroke-width="10"
      opacity="0.5"
      :key="n">
    </line>
  </g>
</template>

<script>
import { mapGetters } from 'vuex'
import * as Mousetrap from 'mousetrap'

export default {
  name: 'GroupSelect',

  props: {
    mouseX: Number,
    mouseY: Number,
    mousedownCount: Number,
    mouseupCount: Number
  },

  data () {
    return {
      sPoints: [],
      ePoints: [],
      linesNum: 0,
      mousedownNum: 0
    }
  },

  computed: {
    isUsingFrameSelect () {
      return this.isUsingBendTool || this.isUsingLinkTool
    },

    ...mapGetters([
      'isUsingBendTool',
      'isUsingLinkTool'
    ])
  },

  methods: {
    restore () {
      this.sPoints = []
      this.ePoints = []
      this.linesNum = 0
      this.mousedownNum = 0
    }
  },

  watch: {
    mousedownCount (newVal) {
      this.mousedownNum++
      if (this.mousedownNum % 2 === 1) {
        this.sPoints.push({
          x: this.mouseX,
          y: this.mouseY
        })
      }
      if (this.mousedownNum % 2 === 0) {
        this.linesNum ++
        this.ePoints.push({
          x: this.mouseX,
          y: this.mouseY
        })
      }
    },

    isUsingBendTool (newVal) {
      if (newVal) {
        this.restore()
      } else {
        this.$store.dispatch('straightenCharacter', {
          sPoints: this.sPoints,
          ePoints: this.ePoints,
          linesNum: this.linesNum
        })
      }
    },

    isUsingSessionBreak (newVal) {
      if (newVal) {
        this.restore()
      } else {
        this.$store.dispatch('breakSession', {
          sPoints: this.sPoints,
          ePoints: this.ePoints,
          linesNum: this.linesNum
        })
      }
    },

    isUsingLinkTool (newVal) {
      if (newVal) {
        this.restore()
      } else {
        this.$store.dispatch('brushingCharacter', {
          sPoints: this.sPoints,
          ePoints: this.ePoints,
          linesNum: this.linesNum
        })
      }
    }
  },

  // IMPORTANT
  mounted () {
    let mousetrap = new Mousetrap()
    mousetrap.bind('esc', () => {
      if (this.isUsingBendTool) {
        this.$store.dispatch('straightenCharacter', {
          sPoints: this.sPoints,
          ePoints: this.ePoints,
          linesNum: this.linesNum
        })
      }
      // if (this.isUsingSessionBreak) {
      //   this.$store.dispatch('breakSession', {
      //     sPoints: this.sPoints,
      //     ePoints: this.ePoints,
      //     linesNum: this.linesNum
      //   })
      // }
      if (this.isUsingLinkTool) {
        this.$store.dispatch('brushingCharacter', {
          sPoints: this.sPoints,
          ePoints: this.ePoints,
          linesNum: this.linesNum
        })
      }
      // if (this.isUsingTwine) {
      //   this.$store.dispatch('twineCharacters', {
      //     sPoints: this.sPoints,
      //     ePoints: this.ePoints,
      //     linesNum: this.linesNum
      //   })
      // }
      this.restore()
    })
  }
}
</script>



