import { Node } from './BezierNode'
import { SplineTwister } from './SplineTwister';

export class StoryLoader {
  constructor (data, sessionTable, frameSteps, curveSteps, storyConfig) {
    this.data = data
    this.sessionTable = sessionTable
    this.frameSteps = frameSteps
    this.curveSteps = curveSteps
    this.positionXTable = new Map()
    this.storyConfig = storyConfig
    this.generatePositionXTable()
  }
  
  generateStorylines () {
    let storylinesData = this.data
    let storylines = []
    for (let i=0, len=storylinesData.length; i<len; i++) {
      let tmpStoryline = storylinesData[i]
      let tmpStorylineSegments = tmpStoryline.points
      let tmpStorylinePoints = this.generateSingleStorylinePair(tmpStorylineSegments, i)
      storylines.push({
        name: tmpStoryline.name,
        characterId: tmpStoryline.character_id,
        points: tmpStorylinePoints
      })
    }
    let twister = new SplineTwister(storylines, this.sessionTable, this.storyConfig)
    twister.twist(1)
    return storylines
  }

  generateSingleStorylinePair (storylineSegments, characterId) {
    let bezierNodes = []
    // do transform here!
    let xBaseline = this.storyConfig.paddingLeft
    let yBaseline = this.storyConfig.paddingTop
    let xScaleFactor = this.storyConfig.xScaleFactor
    let yScaleFactor = this.storyConfig.yScaleFactor
    for (let i=0, len=storylineSegments.length; i<len; i++) {
      let tmpSegment = storylineSegments[i]
      let sTimeStamp = this.getPositionX(i, true) * xScaleFactor + xBaseline
      let eTimeStamp = this.getPositionX(i, false) * xScaleFactor + xBaseline
      let yPos = tmpSegment.item3 > 0 ? tmpSegment.item3 * yScaleFactor + yBaseline : -10000
      let sYPos = this.isStepPoint(i, characterId) ? -5000 : yPos
      let sNode = new Node(sTimeStamp, sYPos)
      let eNode = new Node(eTimeStamp, yPos)
      bezierNodes.push(sNode)
      bezierNodes.push(eNode)
    }
    return bezierNodes
  }

  isStepPoint (frame, characterId) {
    // debugger
    let storylineSegments = this.data[characterId].points
    let frameCount = storylineSegments.length
    if (frame === 0 || frame === frameCount - 1) {
      return false
    }
    // must be in session whose characters less than 3
    let session = this.sessionTable[characterId][frame]
    let sessionCount = 0
    for (let c = 0, len = this.sessionTable.length; c < len; c++) {
      let tmpSession = this.sessionTable[c][frame]
      if (tmpSession === session) {
        sessionCount++
      }
    }
    if (sessionCount > 2) {
      return false
    }
    let lastSegmentY = storylineSegments[frame - 1].item3
    let nextSegmentY = storylineSegments[frame + 1].item3
    let currSegmentY = storylineSegments[frame].item3
    // let threshold = 10
    let result = (currSegmentY - lastSegmentY) * (currSegmentY - nextSegmentY)
    if (result < 0) {
      return true
    } else {
      return false
    }
  }

  getSegmentY (frame, characterId) {
    return this.data[characterId].points[frame].item3
  }

  getPositionX (frame, isLeft) {
    let [leftX, rightX] = this.positionXTable.get(frame)
    return isLeft ? leftX : rightX
  }

  generatePositionXTable () {
    let frameCount = this.sessionTable[0].length
    let currLeftX = 0
    let currRightX = this.frameSteps[0]
    let positionXTable = this.positionXTable
    for (let frame = 0; frame < frameCount; frame++) {
      positionXTable.set(frame, [currLeftX, currRightX])
      currLeftX += (this.frameSteps[frame] + this.curveSteps[frame])
      if (frame < frameCount - 1) {
        currRightX = currLeftX + this.frameSteps[frame + 1]
      }
    }
  }
}



