<template>
  <rect @mousedown="handleMousedown($event)"
    @click="handleClick"
    @contextmenu="handleContextmenu"
    :x="node.x-squareSize/2"
    :y="node.y-squareSize/2"
    :width="squareSize"
    :height="squareSize"
    fill="white"
    stroke="black"
    stroke-width="1">
  </rect>
</template>

<script>
export default {
  name: 'ControlNode',

  props: ['mouseX', 'mouseY', 'mousedownCount', 'mouseupCount', 'node'],

  data () {
    return {
      dragged: false,
      squareSize: 6,
      xInNodeCoordinate: 0,
      yInNodeCoordinate: 0
    }
  },

  methods: {
    handleMousedown () {
      if (event.button === 0) {
        this.dragged = true
      }
      this.xInNodeCoordinate = this.mouseX - this.node.x
      this.yInNodeCoordinate = this.mouseY - this.node.y
    },

    handleClick () {
      // this.node.isActiveBezierTool = !this.node.isActiveBezierTool
      return this.node.isActiveBezierTool ? this.node.closeBezierTool() : this.node.activateBezierTool()
    },

    handleContextmenu () {
      this.$emit('deleteNode')
      return false
    }
  },

  watch: {
    mouseX (newVal) {
      if (this.dragged) {
        this.node.x = newVal - this.xInNodeCoordinate
        if (this.node.x2) {
          this.node.x2 = this.node.x
        }
        if (this.node.x3) {
          this.node.x3 = this.node.x
        }
      }
    },

    mouseY (newVal) {
      if (this.dragged) {
        this.node.y = newVal - this.yInNodeCoordinate
        if (this.node.y2) {
          this.node.y2 = this.node.y
        }
        if (this.node.y3) {
          this.node.y3 = this.node.y
        }
      }
    },

    mouseupCount () {
      this.dragged = false
    }
  }
}
</script>



