<template>
  <g class="storyline"
    @mousedown="handleMousedown"
    @mouseup="handleMouseup"
    v-show="visible">
    <path fill="none"
      :d="pathD"
      stroke="white"
      stroke-width="10"
      opacity="1">
    </path>
    <path fill="none"
      :d="pathD"
      :stroke="strokeColor"
      :stroke-width="strokeWidth"
      :opacity="opacity">
    </path>
    <text class="label"
      @click.stop="handleSelecting"
      @mousedown.stop="beginDragMoving"
      @mouseup="endDragMoving"
      text-anchor="end"
      font-size="16"
      :x="labelX"
      :y="labelY">
      {{ character }}
    </text>
  </g>
</template>

<script>
import { mapGetters } from 'vuex'
import { ADD_FOCUSING_CHARACTER, ADD_SELECTING_CHARACTER } from '../../../store/MutationTypes'
import { StoryReorder, SessionMover } from '../../../types/Selector'
import { CubicSplineSimple } from '../../../types/CubicSpline'
import { PathReshaperSingle } from '../../../types/PathReshaper'
import { UniformSampler } from '../../../types/UniformSampler'
import { TweenMax } from 'gsap'
import * as Mousetrap from 'mousetrap'

export default {
  name: 'Storyline',

  props: ['mouseX', 'mouseY', 'mousedownCount', 'mouseupCount', 'fillColor', 'strokeColor', 'strokeWidth', 'opacity', 'object'],

  data: () => {
    return {
      sampleNodes: [],
      targetPath: [],
      dragMoving: false,
      dashlineRange: [],
      needAnimation: true,
      storyReorder: null,
      sessionMover: null,
      recordY: 0
    }
  },

  computed: {
    nodes () {
      return this.object.nodes
    },

    cleanNodes () {
      return this.nodes.filter(node => node.y > 0)
    },

    samples () {
      return this.sampleNodes
    },

    visible () {
      return this.object.isShow && this.object.name !== 'RABBIT'
    },

    character () {
      return this.object.name
    },

    starwarColor () {
      switch (this.character) {
        case 'VADER':
          return 'black'
        case 'R2-D2':
          return 'blue'
        case 'C-3PO':
          return 'orange'
        case 'CHEWIE':
          return 'brown'
        case 'JABBA':
          return 'green'
        case 'GREEDO':
          return 'green'
        case 'YODA':
          return 'green'
        case 'BOBA FETT':
          return 'green'
        case 'EMPEROR':
          return 'black'
        case 'T-REX':
        case 'RAPTOR1':
        case 'RAPTOR2':
        case 'RAPTOR3':
        case 'DILOPHOSAURUS':
          return 'red'
        case 'SMITH':
          return 'black'
        case 'TRINITY':
          return 'red'
        case 'NEO':
          return 'blue'
        case 'MORPHEUS':
          return 'purple'
        case 'TANK':
          return 'green'
        default:
          return 'gray'
      }
    },

    labelX () {
      return this.sampleNodes.length > 0 && this.nodes.length > 0 ? this.sampleNodes[0].x - 5 : -10000
    },

    labelY () {
      return this.sampleNodes.length > 0 && this.nodes.length > 0 ? this.sampleNodes[0].y + 5 : -10000
    },

    labelXEnd () {
      let n = this.sampleNodes.length - 1
      return n > -1 && this.nodes.length > 0 ? this.sampleNodes[n].x + 5 : -10000
    },

    labelYEnd () {
      let n = this.sampleNodes.length - 1
      return n > -1 && this.nodes.length > 0 ? this.sampleNodes[n].y + 5 : -10000
    },

    bone () {
      let cleanNodes = this.removeInvalidNodes(this.object.nodes)
      this.sampleNodes = cleanNodes
      let d = ''
      cleanNodes.forEach((node, i) => {
        let x = node.x
        let y = node.y
        if (i === 0) {
          d += `M ${x} ${y}`
        } else {
          d += ` L ${x} ${y}`
        }
      })
      return d
    },

    pathD () {
      let cleanNodes = this.cleanNodes
      // this.dashlineRange = this.addDashlineRange()
      // get control points
      if (cleanNodes.length > 0) {
        // get sample points
        let cubicSpline = new CubicSplineSimple(cleanNodes)
        let fittingNodes = cubicSpline.fit()
        let step = this.sampleStep
        let sampleNodes = UniformSampler.sample(fittingNodes, step)
        let wave = this.jitterWave
        UniformSampler.jitter(sampleNodes, wave)
        this.sampleNodes = sampleNodes
        let dashlineStep = this.sampleStep
        // console.log(JSON.stringify({ name: this.character, points: sampleNodes.map(v => { return { x: v.x, y: v.y } }) }))
        return this.calculatePathD(dashlineStep, sampleNodes)
      } else {
        return ''
      }
    },

    sampleStep () {
      return this.storyConfig.sampleStep
    },

    jitterWave () {
      return this.storyConfig.jitterWave
    },

    ...mapGetters([
      'isUsingOrderTool',
      'isUsingBendTool',
      'isUsingCurveTool',
      'isUsingLinkTool',
      'dashRangeMap',
      'storyConfig',
      'storyFrameMap',
      'storySessionMap',
      'storyCharacterMap',
      'storylines',
      'selectedCharacters'
    ])
  },

  methods: {
    handleMousedown () {
      // reorder
      if (this.isUsingOrderTool) {
        this.storyReorder = new StoryReorder(this.storylines, this.storyConfig, this.storyFrameMap, this.storySessionMap, this.storyCharacterMap)
        this.storyReorder.locate(this.mouseX, this.mouseY)
        this.recordY = this.mouseY
      }
      // session mover
      if (this.isUsingCurveTool && this.selectedCharacters.length === 0) {
        this.sessionMover = new SessionMover(this.storylines, this.storyFrameMap, this.storySessionMap, this.storyCharacterMap)
        this.sessionMover.locate(this.mouseX, this.mouseY)
        this.recordY = this.mouseY
      }
    },

    handleMouseup () {
      // reorder
      if (this.isUsingOrderTool && this.storyReorder) {
        let result = this.storyReorder.reorder()
        if (result.frame > -1) {
          this.$store.dispatch('orderSession', result)
        }
        this.storyReorder = null
      }
      // session mover
      if (this.isUsingCurveTool && this.selectedCharacters.length === 0) {
        this.sessionMover = null
      }
    },

    handleSelecting () {
      if (this.isUsingCurveTool || this.isUsingLinkTool) {
        this.$store.commit(ADD_SELECTING_CHARACTER, this.object.characterId)
      }
      if (this.isUsingBendTool) {
        this.$store.commit(ADD_FOCUSING_CHARACTER, this.object.characterId)
      }
      this.$emit('storylineSelect')
    },

    beginDragMoving () {
      this.dragMoving = true
      this.recordY = this.mouseY
    },

    endDragMoving () {
      this.dragMoving = false
      this.recordY = 0
      if (this.isUsingOrderTool) {
        this.$store.dispatch('orderStoryline', this.object.characterId)
      }
    },

    removeInvalidNodes (nodes) {
      let _nodes = []
      for (let i = 0, len = nodes.length; i < len; i++) {
        if (i === 0) {
          if (nodes[i].y > -10000) {
            _nodes.push(nodes[i])
          }
        } else if (i === len - 1) {
          if (nodes[i].y > -10000) {
            _nodes.push(nodes[i])
          }
        } else {
          if (nodes[i].y > -10000) {
            if (Math.abs(nodes[i].y - nodes[i - 1].y) > 1 || Math.abs(nodes[i].y - nodes[i + 1].y) > 1) {
              _nodes.push(nodes[i])
            }
          }
        }
      }
      return _nodes
    },

    appendDashline (node, i, step) {
      let pathD = ''
      let sampleNodes = this.sampleNodes

      if (i === 0) {
        pathD += `M ${node.x} ${node.y}`
      } else {
        let intensity = step / 2
        let endY = node.y
        let startY = sampleNodes[i - 1].y
        let endX = node.x
        let startX = sampleNodes[i - 1].x
        let arc = Math.atan2(endY - startY, endX - startX)
        let yStep = Math.sin(arc) * intensity
        let xStep = Math.cos(arc) * intensity
        let currentX = startX
        let solid = false

        while ((xStep > 0 && currentX + xStep < endX) || (xStep < 0 && currentX + xStep > endX)) {
          if (solid) {
            pathD += ' l'
          } else {
            pathD += ' m'
          }
          pathD += ` ${xStep} ${yStep}`
          solid = !solid
          currentX += xStep
        }
        pathD += ` L ${endX} ${endY}`
      }
      return pathD
    },

    appendCurve (i, wasDashline) {
      let sampleNodes = this.sampleNodes
      let pathD = ''
      if (i < 2) {
        for (let j = 1; j < 2; j++) {
          pathD += ` L ${sampleNodes[i].x} ${sampleNodes[i].y}`
        }
      } else if (wasDashline) {
        if (this.jitterWave > 0) {
          pathD += ` C ${sampleNodes[i - 2].x},${sampleNodes[i - 2].y} ${sampleNodes[i - 1].x},${sampleNodes[i - 1].y} ${sampleNodes[i].x},${sampleNodes[i].y}`
        } else {
          pathD += ` L ${sampleNodes[i].x} ${sampleNodes[i].y}`
        }
      } else {
        if (this.jitterWave > 0) {
          pathD += ` S ${sampleNodes[i - 1].x},${sampleNodes[i - 1].y} ${sampleNodes[i].x},${sampleNodes[i].y}`
        } else {
          pathD += ` L ${sampleNodes[i].x} ${sampleNodes[i].y}`
        }
      }
      return pathD
    },

    calculatePathD (step, sampleNodes) {
      // let sampleNodes = this.sampleNodes
      let pathD = ''
      // line with respect to dashlineRange
      let i = 0
      let wasDashline = false
      for (; i < sampleNodes.length - 3;) {
        const node = sampleNodes[i]
        let inDashline = false
        for (let j = 0; j < this.dashlineRange.length; j++) {
          if (this.dashlineRange[j][0] < node.x && node.x < this.dashlineRange[j][1]) {
            inDashline = true
            break
          }
        }
        if (i === 0) {
          pathD += ` M ${sampleNodes[i].x} ${sampleNodes[i].y}`
          i++
        } else if (inDashline) {
          pathD += this.appendDashline(node, i, step)
          wasDashline = true
          i++
        } else {
          if (wasDashline) {
            i += 3
          }
          pathD += this.appendCurve(i, wasDashline)
          // lead to a bug when dashline begin
          i += 2
          wasDashline = false
        }
      }
      i -= 3
      if (i < 0) {
        i = 0
      }
      for (; i < sampleNodes.length; i++) {
        if (sampleNodes[i]) {
          pathD += ` L ${sampleNodes[i].x} ${sampleNodes[i].y}`
        } else {
          // debugger
        }
      }
      return pathD
    },

    addDashlineRange () {
      switch (this.character) {
        case '阿斯兰':
          return [[100, 10000]]
        case '斯特里克兰夫人':
          return [[380, 10000]]
        case '斯特里克兰':
          return [[0, 300], [350, 400]]
        case '我':
          return [[700, 1800]]
        case 'IMELDA':
          return [[250, 10000]]
        case 'HECTOR':
          return [[230, 10000]]
        case 'CRUZ':
          return [[250, 10000]]
        case 'COCO':
          return [[1000, 10000]]
        case 'MIGUEL':
          return [[400, 830]]
        default:
          return []
      }
    }
  },

  mounted () {
    this.object.init()
    let mousetrap = new Mousetrap()
    mousetrap.bind('esc', () => {
      if (this.isUsingCurveTool && this.selectedCharacters.indexOf(this.object.characterId) > -1) {
        let pathReshaper = new PathReshaperSingle(this.targetPath)
        pathReshaper.moveFocusCharacter(this.object.nodes)
        this.targetPath = []
      }
    })
  },

  watch: {
    mouseY (newY) {
      if (this.dragMoving && this.isUsingOrderTool) {
        if (newY > 0) {
          let distY = newY - this.recordY
          this.object.updateY(distY)
        }
      }
      // reorder
      if (this.isUsingOrderTool && this.storyReorder) {
        let moveY = this.mouseY - this.recordY
        this.storyReorder.move(moveY)
      }
      // session mover
      if (this.isUsingCurveTool && this.selectedCharacters.length === 0 && this.sessionMover) {
        let moveY = this.mouseY - this.recordY
        this.sessionMover.move(moveY)
        // let twister = new SplineTwister(this.storylines, this.storySessionMap, this.storyConfig)
        // twister.twist(1)
      }
    },

    mousedownCount (newVal) {
      // reshape
      if (this.isUsingCurveTool && this.selectedCharacters.indexOf(this.object.characterId) > -1) {
        this.targetPath.push({
          x: this.mouseX,
          y: this.mouseY
        })
      }
    },

    mouseupCount (newVal) {
      if (this.dragMoving && this.isUsingOrderTool) {
        this.endDragMoving()
      }
      // reorder
      if (this.isUsingOrderTool && this.storyReorder) {
        let result = this.storyReorder.reorder()
        if (result.frame > -1) {
          this.$store.dispatch('orderSession', result)
        }
        this.storyReorder = null
      }
      // session mover
      if (this.isUsingCurveTool && this.selectedCharacters.length === 0) {
        this.sessionMover = null
      }
    },

    dashRangeMap (newRange) {
      if (this.selectedCharacters.indexOf(this.object.characterId) > -1) {
        this.dashlineRange = newRange
      }
    },

    nodes (newNodes, oldNodes) {
      // console.log('animate')
      if (this.needAnimation && oldNodes) {
        if (newNodes.length !== oldNodes.length) {
          console.error('mismatch')
        }
        oldNodes.forEach((node, i) => {
          if ((node.x !== newNodes[i].x || node.y !== newNodes[i].y) && node.y > 0) {
            TweenMax.fromTo(this.nodes[i], 1,
              { x: oldNodes[i].x, y: oldNodes[i].y }, { x: newNodes[i].x, y: newNodes[i].y })
          }
        })
      }
    }
  }
}
</script>

<style>
text.label {
  cursor: pointer;
}
</style>



