<template>
  <div class="upload-tool">
    <icon name="plus" scale="2"></icon>
    <input name="upload" type="file" @change="handleUpload">
  </div>
</template>

<script>
import { RESTORE_DEFAULT_TOOLS, UPDATE_REQUEST } from '../../../store/MutationTypes'

export default {
  name: 'UploadTool',

  methods: {
    handleUpload (event) {
      let file = event.target.files[0]
      let type = file.name.split('.')[1]
      if (type === 'xml') {
        this.$store.dispatch('uploadXMLFile', file)
      } else if (type === 'json') {
        let reader = new FileReader()
        reader.onloadend = (event) => {
          if (event.target.readyState === FileReader.DONE) {
            // console.log(event.target.result, typeof (event.target.result))
            let request = JSON.parse(event.target.result)
            this.$store.commit(RESTORE_DEFAULT_TOOLS)
            this.$store.commit(UPDATE_REQUEST, request)
            this.$store.dispatch('updateStoryline')
          }
        }
        reader.readAsText(file)
      }
    }
  }
}
</script>

<style lang="scss">
// @import '../../../assets/Constants.scss';

.upload-tool {
  position: relative;
  width: 50px;
  height: 42px;
  padding: 8px 0 0;
  background: #FFCC00;
  text-align: center;
  color: white;
  margin: 25px 8px;
  border-radius: 50%;
  box-shadow: 2px 2px 1px grey;

  input {
    position: absolute;
    right: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
  }
}
</style>



