import { BezierNode } from './BezierNode'

class State {
  constructor () {
    this._visible = true
    this._scale = false
    this._reshape = false
    this._move = true
    this._select = false
    this._rotate = false
    this._create = false
  }

  get isShow () {
    return this._visible
  }

  get isScale () {
    return this._scale
  }

  get isReshape () {
    return this._reshape
  }

  get isMove () {
    return this._move
  }

  get isSelect () {
    return this._select
  }

  get isRotate () {
    return this._rotate
  }

  get isCreate () {
    return this._create
  }

  activateShow () {
    this._visible = true
  }

  closeShow () {
    this._visible = false
  }

  activateScale () {
    this._scale = true
  }

  closeScale () {
    this._scale = false
  }

  activateReshape () {
    this._reshape = true
  }

  closeReshape () {
    this._reshape = false
  }

  activateMove () {
    this._move = true
  }

  closeMove () {
    this._move = false
  }

  activateSelect () {
    this._select = true
  }

  closeSelect () {
    this._select = false
  }

  activateRotate () {
    this._rotate = true
  }

  closeRotate () {
    this._rotate = false
  }

  initState () {
    this._create = true
    this._move = true
    // this._visible = true
  }
}

// base object for shape and text
class VisualObject extends State {
  constructor (type) {
    super()
    this._id = -1
    this._type = type
    this._name = ''
    this._cardID = 0
  }

  get name () {
    return this._name
  }

  set name (str) {
    this._name = str
  }

  get type () {
    return this._type
  }

  get id () {
    return this._id
  }

  set id (value) {
    this._id = value
  }
}

// base object for customshape, toolshape, and storyline
class Shape extends VisualObject {
  constructor (type) {
    super(type)
    this._frameX = 0
    this._frameY = 0
    this._frameW = 0
    this._frameH = 0
    this._positionX = 0
    this._positionY = 0
    this._offsetX = 0
    this._offsetY = 0
    this.geometry = {
      nodes: []
    }
  }

  get frameX () {
    return this._frameX
  }

  set frameX (value) {
    this._frameX = value
  }

  get frameY () {
    return this._frameY
  }

  set frameY (value) {
    this._frameY = value
  }

  get frameH () {
    return this._frameH
  }

  set frameH (value) {
    this._frameH = value
  }

  get frameW () {
    return this._frameW
  }

  set frameW (value) {
    this._frameW = value
  }

  get positionX () {
    return this._positionX
  }

  get positionY () {
    return this._positionY
  }

  get nodes () {
    return this.geometry.nodes
  }

  set nodes (arr) {
    this.geometry.nodes = arr
  }

  move (moveX, moveY) {
    // move position
    this._positionX += moveX
    this._positionY += moveY
    // move frame
    this._frameX += moveX
    this._frameY += moveY
    // move nodes
    this.geometry.nodes.forEach(node => {
      node.move(moveX, moveY)
    })
  }

  rotate (sin, cos) {
    // rotate nodes
    // debugger
    this.geometry.nodes.forEach(node => {
      node.rotate(sin, cos, this._positionX, this._positionY)
    })
    // update frame
    this.updateFrame()
    // this.updatePosition()
  }

  update (distX, distY) {
    // update nodes
    this.geometry.nodes.forEach(node => {
      node.update(distX, distY)
    })
    // update frame and position
    this.updateFrame()
    this.updatePosition()
  }

  updateX (distX) {
    this.geometry.nodes.forEach(node => {
      if (node.y > -10000) {
        node.updateX(distX)
      }
    })
    this.updateFrame()
    this.updatePosition()
  }

  updateY (distY) {
    this.geometry.nodes.forEach(node => {
      if (node.y > -10000) {
        node.updateY(distY)
      }
    })
    this.updateFrame()
    this.updatePosition()
  }

  updateFrame () {
    let xNodes = this.geometry.nodes.map(node => node.x)
    let yNodes = this.geometry.nodes.map(node => node.y)
    this._frameX = Math.min.apply(null, xNodes)
    this._frameW = Math.max.apply(null, xNodes) - this._frameX
    this._frameY = Math.min.apply(null, yNodes)
    this._frameH = Math.max.apply(null, yNodes) - this._frameY
  }

  updatePosition () {
    this._positionX = this._frameX + this._frameW / 2
    this._positionY = this._frameY + this._frameH / 2
  }

  // TODO
  scale (scale) {

  }

  initTransform () {
    this.geometry.nodes.forEach(node => {
      node.backup()
    })
  }

  addBezierNode (x, y, x2, y2) {
    let bezierNode = new BezierNode(x, y, x2, y2)
    this.geometry.nodes.push(bezierNode)
    return this.geometry.nodes.length - 1
  }

  updateBezierNode (i, x, y, x2, y2) {
    let bezierNode = new BezierNode(x, y, x2, y2)
    if (this.geometry.nodes.length < i) {
      this.geometry.nodes.push(bezierNode)
    } else {
      this.geometry.nodes[i] = bezierNode
    }
  }

  deleteBezierNode (i) {
    this.geometry.nodes.splice(i, 1)
  }

  init () {
    this.updateFrame()
    this.updatePosition()
    this.initState()
  }
}

// Regular Shape
export class RegularShape extends Shape {
  constructor (type) {
    super(type)
  }
}

// Custom Shape
export class CustomShape extends Shape {
  constructor (type) {
    super(type)
  }
}

// Storyline
export class Storyline extends Shape {
  constructor (type) {
    super(type)
    this._characterId = -1
  }

  get characterId () {
    return this._characterId
  }

  set characterId (id) {
    this._characterId = id
  }

  init() {
    this.updateFrame()
    this.updatePosition()
    this.initState()
    this.closeMove()
  }
}

// Tool Shape
export class ToolShape extends Shape {
  constructor (type) {
    super(type)
  }
}

// Text
export class Text extends VisualObject {
  constructor (type, x, y) {
    super (type)
    this.frameX = x
    this.frameY = y
    this._frameX = x
    this._frameY = y
    this.frameW = 0
    this.frameH = 0
    this.fontSize = 12
    this.fontFamily = 'Arial'
    this.color = 'black'
    this.content = 'hello'
    this.initState()
  }

  updateX (newX) {
    this.frameX = this._frameX + newX
  }

  updateY (newY) {
    this.frameY = this._frameY + newY
  }

  beginMove () {
    this._frameX = this.frameX
    this._frameY = this.frameY
  }
}


