<template>
  <scaleframe
    @rotate="rotate($event)"
    :object="object"
    :mouseX="mouseX"
    :mouseY="mouseY"
    :mousedownCount="mousedownCount"
    :mouseupCount="mouseupCount">
  </scaleframe>
</template>

<script>
import ScaleFrame from './ScaleFrame'

export default {
  name: 'RotateFrame',

  props: ['mouseX', 'mouseY', 'mousedownCount', 'mouseupCount', 'object'],

  data () {
    return {
      sin: 0,
      cos: 0
    }
  },

  computed: {
    nodes () {
      return this.object.nodes
    }
  },

  methods: {
    rotate ({sin, cos}) {
      this.sin = sin
      this.cos = cos
      this.object.rotate(sin, cos)
    }
  },

  components: {
    scaleframe: ScaleFrame
  }
}
</script>



