<template>
  <div  v-tooltip="'Scaling'">
    <font-awesome-icon :icon="icon" />
  </div>
</template>

<script>
import Icon from '@fortawesome/fontawesome-free-solid/faExpandArrowsAlt'
export default {
  name: 'ScaleTool',

  computed: {
    icon () {
      return Icon
    }
  }
}
</script>



