import {
  INSERT_VISUAL_OBJECT,
  LOCATE_FOCUSOBJECT,
  UPDATE_STORYLINE
} from '../MutationTypes'
import * as _ from 'lodash'

const state = {
  objectsQueue: [],
  objectsTree: [],
  focusObject: {},
  copiedBuffer: {},
  count: {
    storylines: 0,
    customshapes: 0,
    rectangles: 0,
    triangles: 0,
    pentagons: 0,
    hexagons: 0,
    lines: 0,
    echelons: 0,
    circles: 0
  }
}

const getters = {
  objects: state => state.objectsQueue,
  objectsTree: state => state.objectsTree,
  focusObject: state => state.focusObject
}

const mutations = {
  [INSERT_VISUAL_OBJECT] (state, obj) {
    obj.id = state.objectsQueue.length
    let counts = 0
    switch (obj.type) {
      case 'customshape':
        counts = ++state.count.customshapes
        break
      case 'storyline':
        counts = ++state.count.storylines
        break
      case 'rectangle':
        counts = ++state.count.rectangles
        break
      case 'triangle':
        counts = ++state.count.triangles
        break
      case 'pentagon':
        counts = ++state.count.pentagons
        break
      case 'hexagon':
        counts = ++state.count.hexagons
        break
      case 'echelon':
        counts = ++state.count.echelons
        break
      case 'line':
        counts = ++state.count.lines
        break
      case 'circle':
        counts = ++state.count.circles
        break
      default:
        break
    }
    if (obj.type !== 'storyline') {
      obj.name = `${obj.type}${counts}`
    }
    obj.id = state.objectsQueue.length
    state.objectsQueue.push(obj)
    state.objectsTree.push({
      name: obj.name,
      children: [obj]
    })
  },

  [LOCATE_FOCUSOBJECT] (state, obj) {
    state.focusObject = obj
  },

  [UPDATE_STORYLINE] (state, obj) {
    state.objectsQueue[obj.id].nodes = obj.nodes
    // obj.nodes.forEach((node, i) => {
    //   state.objectsQueue[obj.id].nodes[i].x = node.x
    //   state.objectsQueue[obj.id].nodes[i].y = node.y
    // })
  }
}

const actions = {
  groupObjects ({ state }) {
    let tree = state.objectsTree
    if (tree.length > 0) {
      let children = []
      // only support two-level grouping
      tree.forEach(subtree => {
        subtree.children.forEach(item => {
          if (item.isSelected) {
            children.push(item)
          }
        })
      })
      tree.push({
        name: 'group',
        children: children
      })
    }
  },

  removeStorylines ({ state }) {
    state.objectsQueue = []
    state.objectsTree = []
  },

  scaleFocusObject ({ state }) {
    state.focusObject.activateScale()
    state.focusObject.closeMove()
    state.focusObject.closeReshape()
  },

  reshapeFocusObject ({ state }) {
    state.focusObject.closeScale()
    state.focusObject.closeMove()
    state.focusObject.activateReshape()
  },

  hideFocusObject ({ state }) {
    state.focusObject.closeShow()
  },

  copyFocusObject ({ state }) {
    state.copiedBuffer = state.focusObject
  },

  pasteFocusObject ({ state }, newPosition) {
    let oldX = state.copiedBuffer.positionX
    let oldY = state.copiedBuffer.positionY
    let newX = newPosition.x
    let newY = newPosition.y
    let moveX = newX - oldX
    let moveY = newY - oldY
    let copiedObject = _.cloneDeep(state.copiedBuffer)
    copiedObject.id = state.objectsQueue.length
    // debugger
    copiedObject.move(moveX, moveY)
    copiedObject.name += '-copy'
    state.objectsQueue.push(copiedObject)
    state.objectsTree.push({
      name: copiedObject.name,
      children: [copiedObject]
    })
    // state.copiedBuffer = {}
  },

  cutFocusObject ({ state }) {
    state.copiedBuffer = state.focusObject
    let objectID = state.focusObject.id
    for (let i = objectID + 1, len = state.objectsQueue.length; i < len; i++) {
      let tmpObject = state.objectsQueue[i]
      tmpObject.id--
    }
    state.objectsQueue.splice(objectID, 1)
  },

  cancelFocusObject ({ state }) {
    if (state.focusObject.type !== 'storyline') {
      state.focusObject.activateMove()
    }
    state.focusObject.closeScale()
    state.focusObject.closeReshape()
    state.focusObject.closeSelect()
    state.focusObject = {}
  }
}

// TODO: support multi-level grouping
// function scanObjectsTree (tree, group, newTree) {
//   if (tree) {
//     tree.forEach(subtree => {
//       if (subtree.state.isSelected) {
//         group.children.push(subtree)
//       } else {
//         let newSubTree = _.cloneDeep(subtree)
//         newSubTree.children = []
//         newTree.push(newSubTree)
//         if (subtree.children) {
//           scanObjectsTree(subtree.children, group, newSubTree.children)
//         }
//       }
//     })
//   }
// }

export default {
  state,
  getters,
  mutations,
  actions
}



