<template>
  <div class="side-panel" :style="styleObject">
    <div class="side-panel-header">{{ panelName === 'ShapePanel' ? 'ShapePanel' : panelName }}</div>
    <div class="side-panel-content" v-if="panelName === 'ElementPanel'">
      <tree-panel></tree-panel>
    </div>
    <div class="side-panel-content" v-if="panelName === 'ShapePanel'">
      <shape-panel></shape-panel>
    </div>
    <div class="side-panel-content" v-if="panelName === 'StoryPanel'">
      <story-panel></story-panel>
    </div>
    <div class="side-panel-content" v-if="panelName === 'TextPanel'">
      <text-panel></text-panel>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import TreePanel from './panels/TreePanel/TreePanel'
import ShapePanel from './panels/ShapePanel'
import StoryPanel from './panels/StoryPanel'
import TextPanel from './panels/TextPanel'

export default {
  name: 'SidePanel',

  data () {
    return {
      styleObject: {
        left: '-260px'
      }
    }
  },

  computed: {
    ...mapGetters([
      'panelActivated',
      'panelName'
    ])
  },

  watch: {
    panelActivated (newVal) {
      if (newVal) {
        this.styleObject.left = '80px'
      } else {
        this.styleObject.left = '-260px'
      }
    }
  },

  components: {
    'tree-panel': TreePanel,
    'shape-panel': ShapePanel,
    'story-panel': StoryPanel,
    'text-panel': TextPanel
  }
}
</script>

<style lang="scss">
@import '../../assets/Constants.scss';

div.side-panel {
  position: absolute;
  display: flex;
  flex-direction: column;
  background: white;
  border-right: 1px solid #{$GRAY2};
  width: $APP_PANEL_SIZE;
  height: 100%;
  transition: left 0.5s ease-out;

  .side-panel-header {
    flex: 0 1 65px;
    border-bottom: 1px solid #{$GRAY2};
    font-size: 1.6em;
    font-weight: 700;
    line-height: 65px;
    text-align: center;
    color: $GRAY5;
  }

  .side-panel-content {
    flex: 1 1;
  }
}
</style>




