<template>
  <div class="tool-item"
    :style="styleObject"
    @mouseover="handleMouseover"
    @mouseout="handleMouseout"
    @click="handleClick($event)">
    <div v-if="toolName === 'OrderTool'">
      <order-tool></order-tool>
    </div>
    <div v-if="toolName === 'BendTool'">
      <bend-tool></bend-tool>
    </div>
    <div v-if="toolName === 'ScaleTool'">
      <scale-tool></scale-tool>
    </div>
    <div v-if="toolName === 'CurveTool'">
      <curve-tool></curve-tool>
    </div>
    <div v-if="toolName === 'LinkTool'">
      <link-tool></link-tool>
    </div>
    <div v-if="toolName === 'PenTool'">
      <pen-tool></pen-tool>
    </div>
  </div>
</template>

<script>
import { OPEN_TOOL, CLOSE_TOOL, CLEAR_SELECTING_CHARACTERS } from '../../store/MutationTypes'

import OrderTool from './tools/OrderTool'
import BendTool from './tools/BendTool'
import ScaleTool from './tools/ScaleTool'
import CurveTool from './tools/CurveTool'
import LinkTool from './tools/LinkTool'
import PenTool from './tools/PenTool'

export default {
  name: 'ToolItem',

  props: {
    usingToolName: String,
    toolName: String
  },

  data () {
    return {
      usingToolCard: false,
      activeColor: '#ddd',
      styleObject: {
        background: ''
      }
    }
  },

  watch: {
    usingToolName (newToolName) {
      if (newToolName === this.toolName) {
        this.styleObject.background = this.activeColor
        this.$store.commit(OPEN_TOOL, this.toolName)
        this.usingToolCard = true
      } else {
        this.styleObject.background = ''
        this.$store.commit(CLOSE_TOOL, this.toolName)
        this.$store.commit(CLEAR_SELECTING_CHARACTERS)
        this.usingToolCard = false
      }
    }
  },

  methods: {
    handleClick () {
      if (this.usingToolName === this.toolName) {
        this.$emit('cancelTool', this.toolName)
        this.usingToolCard = !this.usingToolCard
        if (this.usingToolCard) {
          this.styleObject.background = this.activeColor
          this.$store.commit(OPEN_TOOL, this.toolName)
        } else {
          this.styleObject.background = ''
          this.$store.commit(CLOSE_TOOL, this.toolName)
          this.$store.commit(CLEAR_SELECTING_CHARACTERS)
        }
      } else {
        this.$emit('enterTool', this.toolName)
      }
    },

    handleMouseover () {
      if (this.styleObject.background === '') {
        if (this.usingToolName !== this.toolName) {
          this.styleObject.background = this.activeColor
        }
      }
    },

    handleMouseout () {
      if (this.styleObject.background === this.activeColor) {
        if (this.usingToolName !== this.toolName) {
          this.styleObject.background = ''
        }
      }
    }
  },

  components: {
    'order-tool': OrderTool,
    'bend-tool': BendTool,
    'scale-tool': ScaleTool,
    'curve-tool': CurveTool,
    'link-tool': LinkTool,
    'pen-tool': PenTool
  }
}
</script>



