<template>
  <div class="save-tool" @click="handleClick"  v-tooltip="'Save'">
    <icon name="save" scale=1.5></icon>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
// import { RoughDrawer } from '../../../types/RoughDrawer'

export default {
  name: 'UploadTool',

  computed: {
    ...mapGetters([
      'request',
      'history',
      'storylines'
    ])
  },

  methods: {
    handleClick (event) {
      this.handleSaveSvg(event)
      // this.handleSaveConfig()
      // this.handleSaveHistory()
      // RoughDrawer.draw(this.storylines, {
      //   roughness: 1,
      //   stroke: 'grey',
      //   strokeWidth: 3,
      //   simplification: 0.4
      // })
    },

    handleSaveSvg (event) {
      let svgEl = document.getElementsByClassName('svg-content')[0]
      svgEl.setAttribute('xmlns', 'http://www.w3.org/2000/svg')
      let svgData = svgEl.outerHTML
      let preface = '<?xml version="1.0" standalone="no"?>\r\n'
      let svgBlob = new Blob([preface, svgData], {type: 'image/svg+xml;charset=utf-8'})
      let svgUrl = URL.createObjectURL(svgBlob)
      let downloadLink = document.createElement('a')
      downloadLink.href = svgUrl
      downloadLink.download = 'storyline.svg'
      document.body.appendChild(downloadLink)
      downloadLink.click()
      document.body.removeChild(downloadLink)
    },

    handleSaveConfig () {
      let configContent = this.request
      let str = JSON.stringify(configContent)
      let dataUrl = 'data:application/json;charset=utf-8,' + encodeURIComponent(str)
      let downloadLink = document.createElement('a')
      downloadLink.href = dataUrl
      downloadLink.download = 'config.json'
      document.body.appendChild(downloadLink)
      downloadLink.click()
      document.body.removeChild(downloadLink)
    },

    handleSaveHistory () {
      let configContent = {storylines: this.storylines}
      let str = JSON.stringify(configContent)
      let dataUrl = 'data:application/json;charset=utf-8,' + encodeURIComponent(str)
      let downloadLink = document.createElement('a')
      downloadLink.href = dataUrl
      downloadLink.download = 'history.json'
      document.body.appendChild(downloadLink)
      downloadLink.click()
      document.body.removeChild(downloadLink)
    }
  }
}
</script>

<style lang="scss">
@import '../../../assets/Constants.scss';

.save-tool {
  display: inline-block;
  position: relative;
  width: 40px;
  height: 32px;
  padding: 8px 0 0;
  margin: 5px 0 15px 15px;
  background: #FFF;
  text-align: center;
  color: $GRAY4;
  border-radius: 50%;
  border: 1px solid grey;
  // box-shadow: 2px 2px 1px grey;

  &:hover {
    background: $GRAY2;
  }
}
</style>



