<template>
  <icon name="pencil" scale=1.5></icon>
</template>

<script>
import { mapGetters } from 'vuex'
import { CustomShape } from '../../../types/VisualObject'
import { INSERT_VISUAL_OBJECT } from '../../../store/MutationTypes'

export default {
  name: 'PenTool',

  computed: {
    ...mapGetters([
      'isUsingPenTool'
    ])
  },

  watch: {
    isUsingPenTool (newVal) {
      if (newVal) {
        let customShape = new CustomShape('customshape')
        this.$store.commit(INSERT_VISUAL_OBJECT, customShape)
      }
    }
  }
}
</script>


