export class EquivalentIndexFinder {
  constructor(sessionTable) {
    this.sessionTable = sessionTable
  }

  find (character, timeframe) {
    let result = new Map()
    let sessionId = this.sessionTable[character][timeframe]
    if (sessionId === -1 || sessionId === 9999) {
      return result
    }
    let timeframeCount = this.sessionTable[0].length
    for (let i = 0; i < this.sessionTable.length; i++) {
      let timeframeRange = []
      for (let j = 0; j < timeframeCount; j++) {
        if (this.sessionTable[i][j] === sessionId) {
          timeframeRange.push(j)
        }
      }
      if (timeframeRange.length > 0) {
        result.set(i, timeframeRange)
      }
    }
    return result
  }
}



