<template>
  <div id="story-panel">
    <div class="slider-wrapper">
      width
      <tool-slider
        @updateSliderVal="updateCanvasW($event)"
        :modelVal="canvasW"
        :min="1000"
        :max="5000"
        :step="10">
      </tool-slider>
    </div>
    <div class="slider-wrapper">
      height
      <tool-slider
        @updateSliderVal="updateCanvasH($event)"
        :modelVal="canvasH"
        :min="500"
        :max="2000"
        :step="10">
      </tool-slider>
    </div>
    <div class="slider-wrapper">
      xScale
      <tool-slider
        @updateSliderVal="updateXScalingFactor($event)"
        :modelVal="storyConfig.xScaleFactor"
        :min="0"
        :max="1"
        :step="0.01">
      </tool-slider>
    </div>
    <div class="slider-wrapper">
      yScale
      <tool-slider
        @updateSliderVal="updateYScalingFactor($event)"
        :modelVal="storyConfig.yScaleFactor"
        :min="0.5"
        :max="1"
        :step="0.02">
      </tool-slider>
    </div>
    <div class="slider-wrapper">
      Relax
      <tool-slider
        @updateSliderVal="updateRelaxingFactor($event)"
        :modelVal="storyConfig.relaxingFactor"
        :min="0"
        :max="100"
        :step="1">
      </tool-slider>
    </div>
    <div class="slider-wrapper">
      Frame
      <tool-slider
        @updateSliderVal="updateFrameStep($event)"
        :modelVal="storyConfig.frameStep"
        :min="50"
        :max="500"
        :step="1">
      </tool-slider>
    </div>
    <div class="slider-wrapper">
      Curve
      <tool-slider
        @updateSliderVal="updateCurveStep($event)"
        :modelVal="storyConfig.curveStep"
        :min="50"
        :max="500"
        :step="1">
      </tool-slider>
    </div>
    <div class="slider-wrapper">
      Smooth
      <tool-slider
        @updateSliderVal="updateSampleStep($event)"
        :modelVal="storyConfig.sampleStep"
        :min="2"
        :max="20"
        :step="1">
      </tool-slider>
    </div>
    <div class="slider-wrapper">
      Sketch
      <tool-slider
        @updateSliderVal="updateJitterWave($event)"
        :modelVal="storyConfig.jitterWave"
        :min="1"
        :max="5"
        :step="0.2">
      </tool-slider>
    </div>
    <textarea name="storyConfig" cols="30" rows="10" placeholder="story configuration here..." v-model="storyConfig.content"></textarea>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'StoryPanel',

  computed: {
    canvasW () {
      return this.paintingCardList[0].width
    },

    canvasH () {
      return this.paintingCardList[0].height
    },

    ...mapGetters([
      'objects',
      'storyConfig',
      'paintingCardList'
    ])
  },

  methods: {
    updateCanvasW (event) {
      this.paintingCardList[0].geometry.width = event
    },

    updateCanvasH (event) {
      this.paintingCardList[0].geometry.height = event
    },

    updateXScalingFactor (event) {
      this.storyConfig.xScaleFactor = event
    },

    updateYScalingFactor (event) {
      this.storyConfig.yScaleFactor = event
    },

    updateRelaxingFactor (event) {
      this.storyConfig.relaxingFactor = event
    },

    updateFrameStep (event) {
      this.storyConfig.frameStep = event
    },

    updateCurveStep (event) {
      this.storyConfig.curveStep = event
    },

    updateSampleStep (event) {
      this.storyConfig.sampleStep = event
    },

    updateJitterWave (event) {
      this.storyConfig.jitterWave = event
    }
  }
}
</script>

<style lang="scss">
#story-panel {
  padding: 5px 12px;

  .slider-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    margin: 5px 0;

    input {
      width: 165px;
    }

    input[type=range]::-webkit-slider-runnable-track {
      height: 100%;
    }

    input[type=range]::-webkit-slider-thumb {
      -webkit-appearance: none;
      height: 100%;
      width: 20px;
      margin-top: 0;
      border-radius: 50%;
      border: 1px solid #03A9F4;
      opacity: 0;
    }
  }

  textarea {
    width: 230px;
    margin-top: 10px;
    // border: 1px solid #03A9F4;
    border-radius: 5px;
  }
}
</style>




