import { LOCATE_TEXT_OBJECT, RESTORE_TEXT_OBJECT } from '../MutationTypes'

const state = {
  focusText: {},
  textObjects: []
}

const getters = {
  focusText: state => state.focusText,
  textObjects: state => state.textObjects
}

const mutations = {
  [LOCATE_TEXT_OBJECT] (state, obj) {
    state.focusText = obj
  },

  [RESTORE_TEXT_OBJECT] (state) {
    state.focusText = {}
  }
}

const actions = {
  insertTextObject ({ state }, obj) {
    state.textObjects.push(obj)
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}



