import {
  LOCATE_FOCUSOBJECT_STYLE,
  RESTORE_FOCUSOBJECT_STYLE,
  OPEN_FILL_COLOR,
  CLOSE_FILL_COLOR,
  OPEN_STROKE_COLOR,
  CLOSE_STROKE_COLOR
} from '../MutationTypes'

const RGBA_BLACK = {
  rgba: {
    r: 0,
    g: 0,
    b: 0,
    a: 1
  }
}

const RGBA_WHITE = {
  rgba: {
    r: 255,
    g: 255,
    b: 255,
    a: 1
  }
}

const DEFAULT_STYLE = {
  fill: RGBA_WHITE,
  stroke: RGBA_BLACK,
  strokeWidth: 1,
  opacity: 1
}

const state = {
  focusObjectStyle: DEFAULT_STYLE,
  isUsingFillColor: false,
  isUsingStrokeColor: false
}

const getters = {
  objectStyle: state => state.focusObjectStyle,
  isUsingFillColor: state => state.isUsingFillColor,
  isUsingStrokeColor: state => state.isUsingStrokeColor
}

const mutations = {
  [LOCATE_FOCUSOBJECT_STYLE] (state, style) {
    state.focusObjectStyle = style
  },

  [RESTORE_FOCUSOBJECT_STYLE] (state) {
    state.focusObjectStyle = DEFAULT_STYLE
  },

  [OPEN_FILL_COLOR] (state) {
    state.isUsingStrokeColor = false
    state.isUsingFillColor = true
  },

  [CLOSE_FILL_COLOR] (state) {
    state.isUsingFillColor = false
  },

  [OPEN_STROKE_COLOR] (state) {
    state.isUsingFillColor = false
    state.isUsingStrokeColor = true
  },

  [CLOSE_STROKE_COLOR] (state) {
    state.isUsingStrokeColor = false
  }
}

export default {
  state,
  getters,
  mutations
}



