<template>
  <div id="tool-box">
    <upload-tool></upload-tool>
    <save-tool></save-tool>
    <tool-item v-for="(toolName, index) in toolItems"
      :toolName="toolName"
      :usingToolName="usingToolName"
      @enterTool="enterTool($event)"
      @calcelTool="cancelTool($event)"
      :key="index">
    </tool-item>
    <undo-tool></undo-tool>
  </div>
</template>

<script>
import ToolItem from './ToolItem'
import UploadTool from './tools/UploadTool'
import SaveTool from './tools/SaveTool'
import UndoTool from './tools/UndoTool'

export default {
  name: 'ToolBox',

  data () {
    return {
      usingToolName: '',
      toolItems: [
        'OrderTool',
        'BendTool',
        'ScaleTool',
        'CurveTool'
        // 'LinkTool'
        // 'PenTool'
      ]
    }
  },

  methods: {
    enterTool (toolName) {
      this.usingToolName = toolName
    },

    cancelTool (toolName) {
      this.usingToolName = ''
    }
  },

  components: {
    'upload-tool': UploadTool,
    'save-tool': SaveTool,
    'undo-tool': UndoTool,
    'tool-item': ToolItem
  }
}
</script>

<style lang="scss">
@import '../../assets/Constants.scss';

#tool-box {
  position: absolute;
  left: calc(100% - 80px);
  display: flex;
  flex-direction: column;
  width: 80px;
  height: 100%;
  // padding: 0 20px;

  .tool-item {
    display: inline-block;
    position: relative;
    width: 40px;
    height: 32px;
    padding: 8px 0 0;
    margin: 5px 0 15px 15px;
    background: #FFF;
    text-align: center;
    color: $GRAY4;
    border-radius: 50%;
    border: 1px solid grey;
    // box-shadow: 2px 2px 1px grey;

    &:hover {
      background: $GRAY2;
    }
  }
}
</style>



