<template>
  <div class="text-card"
    v-show="visible"
    @contextmenu="handleContextMenu"
    @click.stop="handleClick"
    @mousedown.stop="handleMousedown"
    @mouseup="handleMouseup"
    :style="styleObject">
    {{ content }}
  </div>
</template>

<script>
import { LOCATE_TEXT_OBJECT, RESTORE_TEXT_OBJECT, LOCATE_FOCUSOBJECT } from '../../../store/MutationTypes'

export default {
  name: 'TextObject',

  props: {
    mouse: Object,
    object: Object,
    scrollX: Number,
    scrollY: Number
  },

  data () {
    return {
      isLocated: false,
      isDragged: false,
      recordMouseX: 0,
      recordMouseY: 0
    }
  },

  methods: {
    handleClick () {
      this.isLocated = !this.isLocated
      if (this.isLocated) {
        this.$store.commit(LOCATE_TEXT_OBJECT, this.object)
      } else {
        this.$store.commit(RESTORE_TEXT_OBJECT)
      }
    },

    handleMousedown () {
      if (event.button === 0) {
        this.isDragged = true
        this.object.beginMove()
        this.recordMouseX = this.mouseX
        this.recordMouseY = this.mouseY
      }
    },

    handleMouseup () {
      if (event.button === 0) {
        this.isDragged = false
      }
    },

    handleContextMenu () {
      this.$store.commit(LOCATE_FOCUSOBJECT, this.object)
    }
  },

  computed: {
    visible () {
      return this.object.isShow
    },

    mouseX () {
      return this.mouse.positionX + this.scrollX
    },

    mouseY () {
      return this.mouse.positionY + this.scrollY
    },

    x () {
      return this.object.frameX
    },

    y () {
      return this.object.frameY
    },

    w () {
      return this.object.frameW
    },

    h () {
      return this.object.frameH
    },

    fontSize () {
      return this.object.fontSize
    },

    fontFamily () {
      return this.object.fontFamily
    },

    color () {
      return this.object.color
    },

    content () {
      return this.object.content
    },

    styleObject () {
      return {
        top: `${this.y}px`,
        left: `${this.x}px`,
        fontSize: `${this.fontSize}px`,
        fontFamily: `${this.fontFamily}`,
        color: `${this.color}`,
        cursor: 'move'
      }
    }
  },

  watch: {
    mouseX (newVal) {
      if (this.isDragged) {
        let displacementX = newVal - this.recordMouseX
        this.object.updateX(displacementX)
      }
    },

    mouseY (newVal) {
      if (this.isDragged) {
        let displacementY = newVal - this.recordMouseY
        this.object.updateY(displacementY)
      }
    }
  }
}
</script>

<style lang="scss">
div.text-card {
  position: absolute;
}
</style>


