<template>
  <div v-tooltip="'Twining'">
    <font-awesome-icon :icon="icon" transform="shrink1.5"/>
  </div>
</template>

<script>
import Icon from '@fortawesome/fontawesome-free-solid/faSpinner'

export default {
  name: 'LinkTool',

  computed: {
    icon () {
      return Icon
    }
  }
}
</script>




