<template>
  <input
    type="range"
    :min="min"
    :max="max"
    :step="step"
    :style="styleObject"
    v-model="modelVal">
</template>

<script>
export default {
  name: 'ToolSlider',

  props: {
    min: Number,
    max: Number,
    step: Number,
    modelVal: Number
  },

  computed: {
    styleObject () {
      let ratio = (this.modelVal - this.min) * 100 / (this.max - this.min)
      this.$emit('updateSliderVal', this.modelVal)
      return {
        backgroundSize: `${ratio}% 100%`
      }
    }
  }
}
</script>

<style lang="scss">
input[type=range] {
  -webkit-appearance: none;
  width: 120px;
  border-radius: 5px; /*这个属性设置使填充进度条时的图形为圆角*/
  background: -webkit-linear-gradient(#888, #888) no-repeat;
  background-size: 0% 100%;
}

input[type=range]::-webkit-slider-runnable-track {
  height: 10px;
  border-radius: 5px; /*将轨道设为圆角的*/
  border: 1px solid #888;
  // background: #ccc;
  // box-shadow: 0 1px 1px #def3f8, inset 0 .125em .125em #0d1112; /*轨道内置阴影效果*/
}

input[type=range]:focus {
  outline: none;
}

input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 18px;
  width: 18px;
  margin-top: -5px; /*使滑块超出轨道部分的偏移量相等*/
  background: #fff;
  border-radius: 50%; /*外观设置为圆形*/
  border: 1px solid #888; /*设置边框*/
  // box-shadow: 0 .125em .125em #3b4547; /*添加底部阴影*/
}
</style>




