import {
  RESTORE_DEFAULT_TOOLS,
  OPEN_TOOL,
  CLOSE_TOOL,
  OPEN_TEXT_TOOL,
  CLOSE_TEXT_TOOL
} from '../MutationTypes'

const state = {
  isUsingOrderTool: false,
  isUsingScaleTool: false,
  isUsingBendTool: false,
  isUsingCurveTool: false,
  isUsingLinkTool: false,
  isUsingUndoTool: false,
  isUsingTextTool: false,
  isUsingPenTool: false
}

const getters = {
  isUsingOrderTool: state => state.isUsingOrderTool,
  isUsingBendTool: state => state.isUsingBendTool,
  isUsingScaleTool: state => state.isUsingScaleTool,
  isUsingCurveTool: state => state.isUsingCurveTool,
  isUsingLinkTool: state => state.isUsingLinkTool,
  isUsingUndoTool: state => state.isUsingUndoTool,
  isUsingTextTool: state => state.isUsingTextTool,
  isUsingPenTool: state => state.isUsingPenTool
}

const mutations = {
  [OPEN_TOOL] (state, toolName) {
    switch (toolName) {
      case 'OrderTool':
        state.isUsingOrderTool = true
        break
      case 'BendTool':
        state.isUsingBendTool = true
        break
      case 'ScaleTool':
        state.isUsingScaleTool = true
        break
      case 'CurveTool':
        state.isUsingCurveTool = true
        break
      case 'LinkTool':
        state.isUsingLinkTool = true
        break
      case 'PenTool':
        state.isUsingPenTool = true
        break
      case 'TextTool':
        state.isUsingTextTool = true
        break
      default:
        break
    }
  },

  [CLOSE_TOOL] (state, toolName) {
    switch (toolName) {
      case 'OrderTool':
        state.isUsingOrderTool = false
        break
      case 'BendTool':
        state.isUsingBendTool = false
        break
      case 'ScaleTool':
        state.isUsingScaleTool = false
        break
      case 'CurveTool':
        state.isUsingCurveTool = false
        break
      case 'LinkTool':
        state.isUsingLinkTool = false
        break
      case 'PenTool':
        state.isUsingPenTool = false
        break
      case 'TextTool':
        state.isUsingTextTool = false
        break
      default:
        break
    }
  },

  [RESTORE_DEFAULT_TOOLS] (state) {
    state.isUsingOrderTool = false
    state.isUsingBendTool = false
    state.isUsingScaleTool = false
    state.isUsingCurveTool = false
    state.isUsingLinkTool = false
    state.isUsingUndoTool = false
    state.isUsingTextTool = false
    state.isUsingPenTool = false
  },

  [OPEN_TEXT_TOOL] (state) {
    state.isUsingTextTool = true
  },

  [CLOSE_TEXT_TOOL] (state) {
    state.isUsingTextTool = false
  }
}

export default {
  state,
  getters,
  mutations
}



