import { OPEN_PANEL, CLOSE_PANEL, ACTIVATE_PANEL, CHANGE_PANEL } from '../MutationTypes'

const state = {
  panelActivated: false,
  panelName: ''
}

const getters = {
  panelActivated: state => state.panelActivated,
  panelName: state => state.panelName
}

const mutations = {
  [OPEN_PANEL] (state, name) {
    state.panelActivated = true
    state.panelName = name
  },

  [CLOSE_PANEL] (state) {
    state.panelActivated = false
    state.panelName = ''
  },

  [ACTIVATE_PANEL] (state, name) {
    if (state.panelActivated) {
      state.panelActivated = false
      state.panelName = ''
    } else {
      state.panelActivated = true
      state.panelName = name
    }
  },

  [CHANGE_PANEL] (state, name) {
    state.panelName = name
    state.panelActivated = true
  }
}

export default {
  state,
  getters,
  mutations
}



