<template>
  <!-- <icon name="rotate-right" scale=1.2></icon> -->
  <div  v-tooltip="'Shifting'">
    <font-awesome-icon :icon="icon" />
  </div>
</template>

<script>
  import faSort from '@fortawesome/fontawesome-free-solid/faSort'

  export default {
    name: 'OrderTool',

    computed: {
      icon () {
        return faSort
      }
    }
  }
</script>


