<template>
  <g class="move-frame"
    @mouseup="handleMouseup($event)"
    @mousedown.stop="handleMousedown($event)"
    @click.stop
    @contextmenu="handleContextmenu($event)">
    <rotateframe
      :object="object"
      :mouseX="mouseX"
      :mouseY="mouseY"
      :mousedownCount="mousedownCount"
      :mouseupCount="mouseupCount">
    </rotateframe>
  </g>
</template>

<script>
import { mapGetters } from 'vuex'
import { LOCATE_FOCUSOBJECT } from '../../../store/MutationTypes'
import RotateFrame from './RotateFrame'

export default {
  name: 'MoveFrame',

  props: ['mouseX', 'mouseY', 'mousedownCount', 'mouseupCount', 'object'],

  data () {
    return {
      moveDragged: false,
      objectDragged: false,
      recordedMouseX: 0,
      recordedMouseY: 0,
      displacementX: 0,
      displacementY: 0
    }
  },

  computed: {
    objectDraggedLocked () {
      return !this.object.isMove
    },

    moveDraggedLocked () {
      return !this.object.isSelected
    },

    xScaleFactor () {
      return this.storyConfig.xScaleFactor
    },

    yScaleFactor () {
      return this.storyConfig.yScaleFactor
    },

    ...mapGetters([
      'storyConfig'
    ])
  },

  methods: {
    handleMousedown () {
      if (event.button === 0) {
        this.recordedMouseX = this.mouseX
        this.recordedMouseY = this.mouseY
        this.object.initTransform()
        this.objectDragged = true
      }
    },

    handleMouseup () {
      if (event.button === 0) {
        this.lastDisplacementX = this.displacementX
        this.lastDisplacementY = this.displacementY
        this.objectDragged = false
      }
      this.object.updateFrame()
      this.object.updatePosition()
    },

    handleContextmenu () {
      this.$store.commit(LOCATE_FOCUSOBJECT, this.object)
      return false
    }
  },

  watch: {
    mouseX (newVal) {
      this.displacementX = newVal - this.recordedMouseX
      // drag move
      if (this.objectDragged && !this.objectDraggedLocked) {
        if (this.object.type === 'storyline') {
          this.object.updateX(this.displacementX / this.xScaleFactor)
        } else {
          this.object.updateX(this.displacementX)
        }
      }
    },

    mouseY (newVal) {
      this.displacementY = newVal - this.recordedMouseY
      if (this.objectDragged && !this.objectDraggedLocked) {
        if (this.object.type === 'storyline') {
          this.object.updateY(this.displacementY / this.yScaleFactor)
        } else {
          this.object.updateY(this.displacementY)
        }
      }
    }
  },

  components: {
    rotateframe: RotateFrame
  }
}
</script>



