<template>
  <div class="text-panel">
    <div class="font-wrapper">
      <input type="text" name="fontSize" v-model="focusText.fontSize" placeholder="font size">
    </div>
    <div class="font-wrapper">
      <input type="text" name="fontFamily" v-model="focusText.fontFamily" placeholder="font family">
    </div>
    <div class="font-wrapper">
      <input type="text" name="textColor" v-model="focusText.color" placeholder="text color">
    </div>
    <textarea name="textContent" cols="30" rows="10" v-model="focusText.content" placeholder="insert text content"></textarea>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'TextPanel',

  computed: {
    ...mapGetters([
      'focusText'
    ])
  }
}
</script>

<style lang="scss">
@import '../../../assets/Constants.scss';

.text-panel {
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  // padding: 5px 12px;

  .font-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: center;

    input {
      width: 220px;
      height: 25px;
      margin: 5px 7px;
      padding: 0 5px;
      border: 1px solid #888;
      border-radius: 3px;
    }
  }

  textarea {
    width: 230px;
    margin: 5px 12px;
    border: 1px solod #888;
    border-radius: 5px;
  }
}
</style>



