<template>
  <div id="workspace"
    @click="handleClick($event)"
    @contextmenu="handleContextmenu($event)"
    @mousemove="handleMousemove($event)"
    @mousedown="handleMousedown($event)"
    @mouseup="handleMouseup($event)">
    <div class="painting" @scroll="handleScroll">
      <svg-card v-for="card in paintingCardList"
        :key="card.id"
        :cardObject="card"
        :scrollX="paintingScrollX"
        :scrollY="paintingScrollY"
        :mouse="mouse">
      </svg-card>
    </div>
    <context-menu v-show="isShowContextMenu"
      :scrollX="paintingScrollX"
      :scrollY="paintingScrollY"
      :recordX="contextMenuPositionX"
      :recordY="contextMenuPositionY">
    </context-menu>
    <color-picker class="fillpicker" v-model="objectStyle.fill" v-show="isUsingFillColor"></color-picker>
    <color-picker class="strokepicker" v-model="objectStyle.stroke" v-show="isUsingStrokeColor"></color-picker>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import { Sketch } from 'vue-color'
import ContextMenu from '../menus/ContextMenu'
import SVGCard from './cards/SVGCard'

export default {
  name: 'WorkSpace',

  data () {
    return {
      paintingEl: null,
      isShowContextMenu: false,
      contextMenuPositionX: 0,
      contextMenuPositionY: 0,
      paintingScrollX: 0,
      paintingScrollY: 0,
      mouse: {
        positionX: 0,
        positionY: 0,
        upCount: 0,
        downCount: 0
      }
    }
  },

  computed: {
    ...mapGetters([
      'isUsingFillColor',
      'isUsingStrokeColor',
      'paintingCardList',
      'objectStyle',
      'objects'
    ])
  },

  mounted () {
    this.$store.dispatch('addPaintingCard')
    this.paintingEl = document.getElementById('painting')
  },

  methods: {
    handleMousemove () {
      this.mouse.positionX = event.clientX
      this.mouse.positionY = event.clientY
    },

    handleMousedown () {
      if (event.button === 0) {
        this.mouse.downCount++
      }
    },

    handleMouseup () {
      if (event.button === 0) {
        this.mouse.upCount++
      }
    },

    handleContextmenu () {
      this.contextMenuPositionX = this.mouse.positionX
      this.contextMenuPositionY = this.mouse.positionY
      this.isShowContextMenu = true
    },

    handleClick () {
      this.isShowContextMenu = false
    },

    handleScroll () {
      this.paintingScrollX = this.paintingEl.scrollLeft
      this.paintingScrollY = this.paintingEl.scrollTop
    },

    ...mapActions([
      'addPaintingCard'
    ])
  },

  components: {
    'context-menu': ContextMenu,
    'color-picker': Sketch,
    'svg-card': SVGCard
  }
}
</script>

<style lang="scss">
@import '../../assets/Constants.scss';

#workspace {
  width: 100vw;
  height: 100vh;
  background: $GRAY1D;
  // overflow: hidden;

  .painting {
    width: 100%;
    height: 100%;
    background-attachment: scroll;
    // overflow: hidden;
  
    &::-webkit-scrollbar {
      width: 12px;
      height: 12px;
    }
  
    &::-webkit-scrollbar-track {
      background: $GRAY3;
    }
  
    &::-webkit-scrollbar-thumb {
      background: $THEME_BLUE;
    }
  }

  .vc-sketch {
    position: absolute;
    left: 350px;
  }

  .fillpicker {
    top: 85px;
  }

  .strokepicker {
    top: 115px;
  }
}
</style>



