<template>
  <customshape v-if="object.type === 'customshape'"
    :object="object"
    :mouseX="mouseX"
    :mouseY="mouseY"
    :mousedownCount="mousedownCount"
    :mouseupCount="mouseupCount"
    :fillColor="fillColor"
    :strokeColor="strokeColor"
    :strokeWidth="style.strokeWidth"
    :opacity="style.opacity">
  </customshape>
  <storyline v-else-if="object.type === 'storyline'"
    @storylineSelect="handleStorylineStyle"
    :object="object"
    :mouseX="mouseX"
    :mouseY="mouseY"
    :mousedownCount="mousedownCount"
    :mouseupCount="mouseupCount"
    :fillColor="fillColor"
    :strokeColor="strokeColor"
    :strokeWidth="style.strokeWidth"
    :opacity="style.opacity">
  </storyline>
  <triangleg v-else-if="object.type === 'triangle'"
    :object="object"
    :mouseX="mouseX"
    :mouseY="mouseY"
    :mousedownCount="mousedownCount"
    :mouseupCount="mouseupCount"
    :fillColor="fillColor"
    :strokeColor="strokeColor"
    :strokeWidth="style.strokeWidth"
    :opacity="style.opacity">
  </triangleg>
  <rectangleg v-else-if="object.type === 'rectangle'"
    :object="object"
    :mouseX="mouseX"
    :mouseY="mouseY"
    :mousedownCount="mousedownCount"
    :mouseupCount="mouseupCount"
    :fillColor="fillColor"
    :strokeColor="strokeColor"
    :strokeWidth="style.strokeWidth"
    :opacity="style.opacity">
  </rectangleg>
  <pentagong v-else-if="object.type === 'pentagon'"
    :object="object"
    :mouseX="mouseX"
    :mouseY="mouseY"
    :mousedownCount="mousedownCount"
    :mouseupCount="mouseupCount"
    :fillColor="fillColor"
    :strokeColor="strokeColor"
    :strokeWidth="style.strokeWidth"
    :opacity="style.opacity">
  </pentagong>
  <hexagong v-else-if="object.type === 'hexagon'"
    :object="object"
    :mouseX="mouseX"
    :mouseY="mouseY"
    :mousedownCount="mousedownCount"
    :mouseupCount="mouseupCount"
    :fillColor="fillColor"
    :strokeColor="strokeColor"
    :strokeWidth="style.strokeWidth"
    :opacity="style.opacity">
  </hexagong>
  <lineg v-else-if="object.type === 'line'"
    :object="object"
    :mouseX="mouseX"
    :mouseY="mouseY"
    :mousedownCount="mousedownCount"
    :mouseupCount="mouseupCount"
    :fillColor="fillColor"
    :strokeColor="strokeColor"
    :strokeWidth=3
    :opacity="style.opacity">
  </lineg>
  <echelong v-else-if="object.type === 'echelon'"
    :object="object"
    :mouseX="mouseX"
    :mouseY="mouseY"
    :mousedownCount="mousedownCount"
    :mouseupCount="mouseupCount"
    :fillColor="fillColor"
    :strokeColor="strokeColor"
    :strokeWidth="style.strokeWidth"
    :opacity="style.opacity">
  </echelong>
</template>

<script>
import { LOCATE_FOCUSOBJECT_STYLE, RESTORE_FOCUSOBJECT_STYLE } from '../../../store/MutationTypes'
import CustomShape from '../shapes/CustomShape'
import Storyline from '../shapes/Storyline'
import Triangle from '../shapes/Triangle'
import Rectangle from '../shapes/Rectangle'
import Pentagon from '../shapes/Pentagon'
import Hexagon from '../shapes/Hexagon'
import Line from '../shapes/Line'
import Echelon from '../shapes/Echelon'

export default {
  name: 'StyleFrame',

  props: ['mouseX', 'mouseY', 'mousedownCount', 'mouseupCount', 'object'],

  data () {
    return {
      isStorylineSelected: false,
      style: {
        fill: {
          rgba: {
            r: 255,
            g: 255,
            b: 255,
            a: 1
          }
        },
        stroke: {
          rgba: {
            r: 0,
            g: 0,
            b: 0,
            a: 1
          }
        },
        strokeWidth: 3,
        opacity: 1
      }
    }
  },

  methods: {
    handleStorylineStyle () {
      this.isStorylineSelected = !this.isStorylineSelected
      if (this.isStorylineSelected) {
        this.$store.commit(LOCATE_FOCUSOBJECT_STYLE, this.style)
      } else {
        this.$store.commit(RESTORE_FOCUSOBJECT_STYLE)
      }
    }
  },

  computed: {
    fillColor () {
      let r = this.style.fill.rgba.r
      let g = this.style.fill.rgba.g
      let b = this.style.fill.rgba.b
      let a = this.style.fill.rgba.a
      return 'rgba(' + r + ',' + g + ',' + b + ',' + a + ')'
    },

    selected () {
      return this.object.isSelected
    },

    strokeColor () {
      if (this.selected) {
        return 'rgba(' + 0 + ',' + 0 + ',' + 255 + ',' + 1 + ')'
      }
      let r = this.style.stroke.rgba.r
      let g = this.style.stroke.rgba.g
      let b = this.style.stroke.rgba.b
      let a = this.style.stroke.rgba.a
      return 'rgba(' + r + ',' + g + ',' + b + ',' + a + ')'
    },

    strokeWidth () {
      if (this.selected) {
        return 1
      } else {
        return this.style.strokeWidth
      }
    },

    isScale () {
      return this.object.isScale
    }
  },

  watch: {
    isScale (newVal) {
      if (newVal) {
        this.$store.commit(LOCATE_FOCUSOBJECT_STYLE, this.style)
      } else {
        this.$store.commit(RESTORE_FOCUSOBJECT_STYLE)
      }
    }
  },

  components: {
    customshape: CustomShape,
    storyline: Storyline,
    triangleg: Triangle,
    rectangleg: Rectangle,
    pentagong: Pentagon,
    hexagong: Hexagon,
    lineg: Line,
    echelong: Echelon
  }
}
</script>



