<template>
  <g class="path-select" v-show="isUsingCurveTool">
    <path
      :d="pathD"
      fill="none"
      stroke="blue"
      stroke-width="5"
    ></path>
  </g>
</template>

<script>
import { mapGetters } from 'vuex'
import * as Mousetrap from 'mousetrap'

export default {
  name: 'PathSelect',

  props: {
    mouseX: Number,
    mouseY: Number,
    mousedownCount: Number
  },

  data () {
    return {
      points: []
    }
  },

  computed: {
    pathD () {
      let d = ''
      this.points.forEach((point, index) => {
        if (index === 0) {
          d = `M ${point.x} ${point.y}`
        } else if (index > 0) {
          d += ` L ${point.x} ${point.y}`
        }
      })
      return d
    },

    ...mapGetters([
      'isUsingCurveTool',
      'selectedCharacters'
    ])
  },

  methods: {
    restore () {
      this.points = []
    }
  },

  watch: {
    mousedownCount (newVal) {
      if (this.selectedCharacters.length > 0) {
        this.points.push({
          x: this.mouseX,
          y: this.mouseY
        })
      }
    },

    isUsingCurveTool (newVal) {
      if (newVal) {
        this.restore()
      } else if (this.points.length > 0) {
        this.$store.dispatch('reshapeCharacters', this.points)
      }
    }
  },

  // IMPORTANT
  mounted () {
    let mousetrap = new Mousetrap()
    mousetrap.bind('esc', () => {
      this.restore()
    })
  }
}
</script>



