<template>
  <g @mouseup="handleMouseup($event)"
    @mousedown="handleMousedown($event)">
    <styleframe :object="object"
      :mouseX="mouseX"
      :mouseY="mouseY"
      :mousedownCount="mousedownCount"
      :mouseupCount="mouseupCount">
    </styleframe>
    <rectframe v-if="isScaleShow"
      @updateX="scaleByX($event)"
      @updateY="scaleByY($event)"
      @updateW="scaleByW($event)"
      @updateH="scaleByH($event)"
      @updateFrame="updateFrame($event)"
      @rotate="rotate($event)"
      :mouseX="mouseX"
      :mouseY="mouseY"
      :frameX="frameX"
      :frameY="frameY"
      :frameW="frameW"
      :frameH="frameH">
    </rectframe>
  </g>
</template>

<script>
import _ from 'lodash'
import { mapGetters } from 'vuex'
import RectFrame from './RectFrame'
import StyleFrame from './StyleFrame'

export default {
  name: 'ScaleFrame',

  props: ['mouseX', 'mouseY', 'mousedownCount', 'mouseupCount', 'object'],

  data () {
    return {
      copyNodes: []
    }
  },

  computed: {
    nodes () {
      return this.object.nodes
    },

    frameX () {
      return this.object.frameX
    },

    frameY () {
      return this.object.frameY
    },

    frameW () {
      return this.object.frameW
    },

    frameH () {
      return this.object.frameH
    },

    isScaleShow () {
      if (this.object.type === 'storyline') {
        return false
      }
      if (this.object.type === 'line') {
        return false
      }
      if (this.nodes.length < 3) {
        return false
      }
      return this.object.isScale
    },

    ...mapGetters([
      'isUsingPenTool'
    ])
  },

  methods: {
    handleMousedown () {
      if (event.button === 0) {
        this.mousedownTime = Date.now()
        this.copyNodes = _.cloneDeep(this.nodes)
      }
    },

    handleMouseup () {
      if (event.button === 0) {
        let _mousedownTime = Date.now()
        let clickTime = _mousedownTime - this.mousedownTime
        if (clickTime < 200) {
          if (!this.object.isReshape) {
            if (this.object.isScale) {
              this.object.closeScale()
              if (this.object.type !== 'storyline') {
                this.object.activateMove()
              }
            } else {
              if (!this.isUsingPenTool) {
                this.object.activateScale()
              }
              this.object.closeMove()
            }
          }
        } else {
          this.mousedownTime = _mousedownTime
        }
      }
    },

    scaleByX (val) {
      let scaleRatio = val / this.frameW
      let anchor = this.frameX + this.frameW
      this.updateNodeX(scaleRatio, anchor)
    },

    scaleByY (val) {
      let scaleRatio = val / this.frameH
      let anchor = this.frameY + this.frameH
      this.updateNodeY(scaleRatio, anchor)
    },

    scaleByW (val) {
      let scaleRatio = val / this.frameW
      let anchor = this.frameX
      this.updateNodeX(scaleRatio, anchor)
    },

    scaleByH (val) {
      let scaleRatio = val / this.frameH
      let anchor = this.frameY
      this.updateNodeY(scaleRatio, anchor)
    },

    updateNodeX (ratio, anchor) {
      this.nodes.forEach((node, index) => {
        let copyNode = this.copyNodes[index]
        node._x = (copyNode._x - anchor) * ratio + anchor
        node._x2 = (copyNode._x2 - anchor) * ratio + anchor
        node._x3 = (copyNode._x3 - anchor) * ratio + anchor
      })
    },

    updateNodeY (ratio, anchor) {
      this.nodes.forEach((node, index) => {
        let copyNode = this.copyNodes[index]
        node._y = (copyNode._y - anchor) * ratio + anchor
        node._y2 = (copyNode._y2 - anchor) * ratio + anchor
        node._y3 = (copyNode._y3 - anchor) * ratio + anchor
      })
    },

    updateFrame (val) {
      this.object.frameX = val.x
      this.object.frameY = val.y
      this.object.frameW = val.w
      this.object.frameH = val.h
      // update position
      this.object.updatePosition()
    },

    rotate ({sin, cos}) {
      this.$emit('rotate', { sin, cos })
    }
  },

  components: {
    styleframe: StyleFrame,
    rectframe: RectFrame
  }
}
</script>



