// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import store from './store'

// Icon List
import 'vue-awesome/icons/arrows'
import 'vue-awesome/icons/pencil'
import 'vue-awesome/icons/send'
import 'vue-awesome/icons/square-o'
import 'vue-awesome/icons/text-width'
import 'vue-awesome/icons/rotate-right'
import 'vue-awesome/icons/rotate-left'
import 'vue-awesome/icons/file-text'
import 'vue-awesome/icons/file-image-o'
import 'vue-awesome/icons/square'
import 'vue-awesome/icons/sliders'
import 'vue-awesome/icons/bold'
import 'vue-awesome/icons/italic'
import 'vue-awesome/icons/font'
import 'vue-awesome/icons/text-height'
import 'vue-awesome/icons/map'
import 'vue-awesome/icons/navicon'
import 'vue-awesome/icons/plus'
import 'vue-awesome/icons/save'

import Icon from 'vue-awesome/components/Icon'
import TreeNode from './components/panelbar/panels/TreePanel/TreeNode'
import ToolSlider from './components/utils/ToolSlider'
import FontAwesomeIcon from '@fortawesome/vue-fontawesome'
import { VTooltip, VPopover, VClosePopover } from 'v-tooltip'

Vue.directive('tooltip', VTooltip)
Vue.directive('close-popover', VClosePopover)
Vue.component('v-popover', VPopover)
Vue.component('icon', Icon)
Vue.component('tree-node', TreeNode)
Vue.component('tool-slider', ToolSlider)
Vue.component('font-awesome-icon', FontAwesomeIcon)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  store,
  template: '<App/>',
  components: { App, Icon }
})



