<template>
  <div class="undo-tool" @click="handleClick"  v-tooltip="'Undo'">
    <icon name="rotate-left" scale=1.5></icon>
  </div>
</template>

<script>
import { UNDO } from '../../../store/MutationTypes'
export default {
  name: 'UndoTool',

  methods: {
    // only withdraw one step
    handleClick () {
      console.log('undo-tool')
      this.$store.dispatch(UNDO)
      this.$store.dispatch('cancelReshape')
    }
  }
}
</script>

<style lang="scss">
@import '../../../assets/Constants.scss';

.undo-tool {
  display: inline-block;
  position: relative;
  width: 40px;
  height: 32px;
  padding: 8px 0 0;
  margin: 5px 0 15px 15px;
  background: #FFF;
  text-align: center;
  color: $GRAY4;
  border-radius: 50%;
  border: 1px solid grey;
  // box-shadow: 2px 2px 1px grey;

  &:hover {
    background: $GRAY2;
  }
}
</style>


