<template>
  <move-frame
    :style="cursorStyle"
    :object="object"
    :mouseX="mouseX"
    :mouseY="mouseY"
    :mouseupCount="mouseupCount"
    :mousedownCount="mousedownCount">
  </move-frame>
</template>

<script>
import { mapGetters } from 'vuex'
import MoveFrame from '../frames/MoveFrame'

export default {
  name: 'VisualObject',

  props: {
    mouseX: Number,
    mouseY: Number,
    mousedownCount: Number,
    mouseupCount: Number,
    object: Object
  },

  computed: {
    cursorStyle () {
      let cursorType = 'move'
      if (this.object.type === 'storyline') {
        cursorType = 'pointer'
      } else if (this.object.isScale) {
        cursorType = 'default'
      }
      if (this.isUsingCurveTool) {
        cursorType = 'crosshair'
      }
      if (this.isUsingBendTool) {
        cursorType = 'crosshair'
      }
      if (this.isUsingScaleTool) {
        cursorType = 'crosshair'
      }
      return {
        'cursor': cursorType
      }
    },

    ...mapGetters([
      'isUsingCurveTool',
      'isUsingBendTool',
      'isUsingScaleTool'
    ])
  },

  components: {
    'move-frame': MoveFrame
  }
}
</script>



