<template>
  <div id="app">
    <workspace></workspace>
    <side-panel></side-panel>
    <side-panelbar></side-panelbar>
    <tool-box></tool-box>
  </div>
</template>

<script>
import ToolBox from './components/toolbox/ToolBox'
import WorkSpace from './components/workspace/WorkSpace'
import SidePanelBar from './components/panelbar/SidePanelBar'
import SidePanel from './components/panelbar/SidePanel'

export default {
  name: 'App',

  mounted () {
    // 全局禁止右键菜单
    document.oncontextmenu = function () {
      return false
    }
  },

  components: {
    'tool-box': ToolBox,
    'workspace': WorkSpace,
    'side-panelbar': SidePanelBar,
    'side-panel': SidePanel
  }
}
</script>

<style lang="scss">
@import './assets/Constants.scss';

body {
  margin: 0px;
  font-family: Bradley Hand ITC;
  user-select: none;

  #app {
    display: flex;
    flex-direction: row;
    position: relative;
  }
}
</style>



