<template>
  <div class="side-panel-bar">
    <div class="side-panel-item" @click="handleElementPanel">
      <icon name="navicon" scale=2></icon>
      <span>Element</span>
    </div>
    <div class="side-panel-item" @click="handleShapePanel">
      <icon name="map" scale=2></icon>
      <span>Shape</span>
    </div>
    <div class="side-panel-item" @click="handleTextPanel">
      <icon name="text-width" scale=2></icon>
      <span>Text</span>
    </div>
    <div class="side-panel-item" @click="handleStoryPanel">
      <icon name="file-text" scale=2></icon>
      <span>Story</span>
    </div>
  </div>
</template>

<script>
import {
  CLOSE_TOOL,
  OPEN_PANEL,
  CLOSE_PANEL,
  OPEN_TEXT_TOOL,
  CLOSE_TEXT_TOOL,
  CLOSE_FILL_COLOR,
  CLOSE_STROKE_COLOR
} from '../../store/MutationTypes'

export default {
  name: 'SidePanelBar',

  data () {
    return {
      usingElementPanel: false,
      usingShapePanel: false,
      usingTextPanel: false,
      usingStoryPanel: false
    }
  },

  methods: {
    handleElementPanel () {
      this.usingElementPanel = !this.usingElementPanel
      this.switchPanel('ElementPanel', this.usingElementPanel)
    },

    handleShapePanel () {
      this.usingShapePanel = !this.usingShapePanel
      this.switchPanel('ShapePanel', this.usingShapePanel)
    },

    handleTextPanel () {
      this.usingTextPanel = !this.usingTextPanel
      this.switchPanel('TextPanel', this.usingTextPanel)
    },

    handleStoryPanel () {
      this.usingStoryPanel = !this.usingStoryPanel
      this.switchPanel('StoryPanel', this.usingStoryPanel)
    },

    switchPanel (panel, isOpen) {
      this.resetPanel()
      if (isOpen) {
        switch (panel) {
          case 'ElementPanel':
            this.usingElementPanel = true
            break
          case 'ShapePanel':
            this.usingShapePanel = true
            break
          case 'TextPanel':
            this.usingTextPanel = true
            this.$store.commit(OPEN_TEXT_TOOL)
            break
          case 'StoryPanel':
            this.usingStoryPanel = true
            break
          default:
            break
        }
        this.$store.commit(OPEN_PANEL, panel)
      } else {
        this.$store.commit(CLOSE_PANEL, panel)
      }
    },

    resetPanel () {
      this.usingElementPanel = false
      this.usingShapePanel = false
      this.$store.commit(CLOSE_FILL_COLOR)
      this.$store.commit(CLOSE_STROKE_COLOR)
      this.$store.commit(CLOSE_TOOL, 'PenTool')
      this.usingTextPanel = false
      this.$store.commit(CLOSE_TEXT_TOOL)
      this.usingStoryPanel = false
    }
  }
}
</script>

<style lang="scss">
@import '../../assets/Constants.scss';

.side-panel-bar {
  position: absolute;
  width: 80px;
  height: 100%;
  background: white;
  border-right: 1px solid #{$GRAY1};
  // box-shadow: 3px 3px 2px grey;

  .side-panel-item {
    padding-top: 15px;
    width: 80px;
    height: 65px;
    text-align: center;
    color: $THEME_BLUE;
    border-bottom: 1px solid #{$GRAY1};

    span {
      display: block;
    }

    &:hover {
      background: $GRAY1D;
    }
  }
}
</style>



