<template>
  <div class="shape-panel">
    <div class="style-card">
      <div class="color-wrapper" @click="handleFillColor">
        Fill
        <input type="text" name="fillColor" :style="fillColor" readonly="readonly">
      </div>
      <div class="color-wrapper" @click="handleStrokeColor">
        Stroke
        <input type="text" name="strokeColor" :style="strokeColor" readonly="readonly">
      </div>
      <div class="slider-wrapper">
        Width
        <tool-slider
          @updateSliderVal="updateStrokeWidth($event)"
          :modelVal="objectStyle.strokeWidth"
          :min="0"
          :max="10"
          :step="0.1">
        </tool-slider>
      </div>
      <div class="slider-wrapper">
        Opacity
        <tool-slider
          @updateSliderVal="updateOpacity($event)"
          :modelVal="objectStyle.opacity"
          :min="0"
          :max="1"
          :step="0.01">
        </tool-slider>
      </div>
    </div>
    <div class="shape-row">
      <div class="shape-item" @click="handleRectangle">
        <!-- <svg class="shape-item-svg">
          <rect x="13" y="20" width="60" height="40" style="fill:none;stroke-width:2;stroke:#333"/>
          <text x="20" y="42" font-size="14">Rectangle</text>
        </svg> -->
        Rectangle
      </div>
      <div class="shape-item" @click="handleTriangle">
        <!-- <svg class="shape-item-svg">
          <rect x="13" y="20" width="60" height="40" style="fill:none;stroke-width:2;stroke:#333"/>
          <text x="20" y="42" font-size="14">Triangle</text>
        </svg> -->
        Triangle
      </div>
      <div class="shape-item" @click="handlePentagon">
        <!-- <svg class="shape-item-svg">
          <rect x="13" y="20" width="60" height="40" style="fill:none;stroke-width:2;stroke:#333"/>
          <text x="20" y="42" font-size="14">Pentagon</text>
        </svg> -->
        Pentagon
      </div>      
    </div>
    <div class="shape-row">
      <div class="shape-item" @click="handleHexagon">
        <!-- <svg class="shape-item-svg">
          <rect x="13" y="20" width="60" height="40" style="fill:none;stroke-width:2;stroke:#333"/>
          <text x="20" y="42" font-size="14">Hexagon</text>
        </svg> -->
        Hexagon
      </div>
      <div class="shape-item" @click="handleEchelon">
        <!-- <svg class="shape-item-svg">
          <rect x="13" y="20" width="60" height="40" style="fill:none;stroke-width:2;stroke:#333"/>
          <text x="20" y="42" font-size="14">Echelon</text>
        </svg> -->
        Echelon
      </div>
      <div class="shape-item" @click="handleLine">
        <!-- <svg class="shape-item-svg">
          <rect x="13" y="20" width="60" height="40" style="fill:none;stroke-width:2;stroke:#333"/>
          <text x="20" y="42" font-size="14">Line</text>
        </svg> -->
        Custom
      </div>      
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { RegularShape, CustomShape } from '../../../types/VisualObject'
import VisualObject from '../../workspace/objects/VisualObject'
import {
  INSERT_VISUAL_OBJECT,
  OPEN_FILL_COLOR,
  CLOSE_FILL_COLOR,
  OPEN_STROKE_COLOR,
  CLOSE_STROKE_COLOR,
  OPEN_TOOL,
  CLOSE_TOOL
} from '../../../store/MutationTypes'

export default {
  name: 'ShapePanel',

  data () {
    return {
      shapeType: ''
    }
  },

  computed: {
    fillColor () {
      return {
        'background': this.objectStyle.fill.hex
      }
    },

    strokeColor () {
      return {
        'background': this.objectStyle.stroke.hex
      }
    },

    ...mapGetters([
      'isUsingFillColor',
      'isUsingStrokeColor',
      'isUsingPenTool',
      'objectStyle',
      'focusText'
    ])
  },

  methods: {
    handleRectangle () {
      this.insertShape('rectangle')
      this.$store.commit(CLOSE_TOOL, 'PenTool')
    },

    handleHexagon () {
      this.insertShape('hexagon')
      this.$store.commit(CLOSE_TOOL, 'PenTool')
    },

    handlePentagon () {
      this.insertShape('pentagon')
      this.$store.commit(CLOSE_TOOL, 'PenTool')
    },

    handleTriangle () {
      this.insertShape('triangle')
      this.$store.commit(CLOSE_TOOL, 'PenTool')
    },

    handleEchelon () {
      this.insertShape('echelon')
      this.$store.commit(CLOSE_TOOL, 'PenTool')
    },

    handleLine () {
      // this.insertShape('line')
      if (this.isUsingPenTool) {
        this.$store.commit(CLOSE_TOOL, 'PenTool')
      } else {
        this.$store.commit(OPEN_TOOL, 'PenTool')
      }
    },

    insertShape (type) {
      if (type) {
        let insertShape = new RegularShape(type)
        this.$store.commit(INSERT_VISUAL_OBJECT, insertShape)
      }
    },

    updateOpacity (event) {
      this.objectStyle.opacity = event
    },

    updateStrokeWidth (event) {
      this.objectStyle.strokeWidth = event
    },

    handleFillColor () {
      if (this.isUsingFillColor) {
        this.$store.commit(CLOSE_FILL_COLOR)
      } else {
        this.$store.commit(OPEN_FILL_COLOR)
      }
    },

    handleStrokeColor () {
      if (this.isUsingStrokeColor) {
        this.$store.commit(CLOSE_STROKE_COLOR)
      } else {
        this.$store.commit(OPEN_STROKE_COLOR)
      }
    }
  },

  watch: {
    isUsingPenTool (newVal) {
      if (newVal) {
        let customShape = new CustomShape('customshape')
        this.$store.commit(INSERT_VISUAL_OBJECT, customShape)
      }
    }
  },

  components: {
    'visual-object': VisualObject
  }
}
</script>

<style lang="scss">
@import '../../../assets/Constants.scss';

.shape-panel {
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  flex-direction: column;

  .shape-row {
    display: flex;
    flex-direction: row;
    flex: 0 1 80px;

    .shape-item {
      flex: 1 1;
      text-align: center;
      margin: 5px;
      padding-top: 25px;
      border: 1px solid #888;
      font-size: 16px;
      
      &:hover {
        background: $GRAY1D;
      }

      &:active {
        background: $GRAY0;
      }

      .shape-item-svg {
        width: 100%;
        height: 100%;
      }
    }
  }

  .style-card {
    padding: 5px 12px;

    .slider-wrapper {
      display: flex;
      flex-direction: row;
      justify-content: flex-end;
      margin: 5px 0;

      input {
        width: 165px;
      }

      input[type=range]::-webkit-slider-runnable-track {
        height: 100%;
      }

      input[type=range]::-webkit-slider-thumb {
        -webkit-appearance: none;
        height: 100%;
        width: 20px;
        margin-top: 0;
        border-radius: 50%;
        border: 1px solid #888;
        opacity: 0;
      }
    }

    .color-wrapper {
      display: flex;
      flex-direction: row;
      justify-content: flex-end;
      margin: 5px 0;

      input {
        width: 164px;
        margin-left: 2px;
        border: 1px solid #888;
        border-radius: 2px;
      }
    }

    textarea {
      width: 230px;
      margin-top: 10px;
      border-radius: 5px;
    }
  }
}
</style>




