import { StyleNode } from './BezierNode'

export class UniformSampler {
  // assume nodes are list in a ascending order 
  static sample(nodes, step) {
    let result = []
    for (let i = 1; i < nodes.length; i++) {
      let lastNode = nodes[i - 1]
      let thisNode = nodes[i]

      let arc = Math.atan2(thisNode.y - lastNode.y, thisNode.x - lastNode.x)
      let yStep = Math.sin(arc) * step
      let xStep = Math.cos(arc) * step
      for (let x = lastNode.x, y = lastNode.y; x < thisNode.x; x += xStep, y += yStep) {
        result.push(new StyleNode(x, y))
      }
    }
    result.push(nodes[nodes.length - 1])
    return result
  }

  static jitter(nodes, range) {
    for (let i = 1; i < nodes.length; i++) {
      let lastNode = nodes[i - 1]
      let thisNode = nodes[i]
      let step = UniformSampler.randomJitter(range)
      let arc = Math.atan2(thisNode.y - lastNode.y, thisNode.x - lastNode.x) + Math.PI / 2
      let yStep = Math.sin(arc) * step
      let xStep = Math.cos(arc) * step
      
      nodes[i].y += yStep
      nodes[i].x += xStep
    }
    return nodes
  }

  static randomJitter(range) {
    return (Math.random() - 0.5) * range
  }
}


